<template>
    <div
      id="pop_volume"
      ref="popover"
      v-if="popVolumeVisible"
      :class="[theme]"
      :style="{
        left: popStartPoint[0] + 'px',
        top: (popStartPoint[1]+10) + 'px',
      }"
    >

    </div>
</template>

<script>
import hitTest from "../utils/hitTest";

export default {
  data() {
    return {};
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    popVolumeVisible() {
      return this.$store.state.popVolumeVisible;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    }
  },
  mounted() {},
  watch: {
    popVolumeVisible: function(newVal) {
      if (newVal) {
        window.addEventListener("mousedown", this.onMouseDown);
      } else {
        window.removeEventListener("mousedown", this.onMouseDown);
      }
    }
  },
  methods: {
    onMouseDown(evt) {
      if (
        this.$refs.popover &&
        !hitTest(
          document.querySelector("#controls .btn_volume"),
          evt.pageX,
          evt.pageY
        ) &&
        !hitTest(this.$refs.popover, evt.pageX, evt.pageY)
      ) {
        this.$store.commit("popVolumeVisible", false);
      }
    },
    close(evt) {
      this.$store.commit("popVolumeVisible", false);
    }
  }
};
</script>

<style lang="scss">
#pop_volume {
  transition: all 0.2s ease-out;
  position: absolute;
  width: 220px;
  height: 80px;
  transform: rotate(0deg);
  transform: translate(-50%, -100%);
  pointer-events: auto;
  background: url(../assets/img/ctrl-volume.svg) no-repeat center;
}



</style>
