<template>
  <portal
    to="portal-chat"
    :disabled="!chatPanelPopouted"
    id="chat_panel"
    ref="panel"
    :style="{height: height}"
    :class="[theme, {cls: chatPanelClsed}]"
  >
    <panel-header
      :title="title"
      :type="'chat'"
      :floating="chatPanelPopouted"
      @close="close"
      @collapse="collapse"
      @popout="popout"
    ></panel-header>
    <div class="scrollpane" v-show="!chatPanelClsed">
      <div class="list">
        <chat-item
          v-for="(p, index) in chatMsgList"
          :key="index"
          :name="p.name"
          :to="p.to"
          :msg="p.msg"
        ></chat-item>
      </div>
    </div>
    <div class="inputbox" v-show="!chatPanelClsed">
      <div class="selectbox">
        <span class="lb">To:</span>
        <span class="name">Everyone</span>
        <div class="ico">
          <span class="md-icon icon icon-arrow-down_12"></span>
        </div>
      </div>
      <div class="tawrap">
        <textarea v-model="message" class placeholder v-on:keyup.enter="sendmsg"></textarea>
      </div>
    </div>
  </portal>
</template>

<script>

export default {
  data() {
    return {
      title: "Chat",
      message: ""
    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    height() {
      if (this.fixHeight && !this.fullscreen) {
        return this.videoLayoutMode != "side-by-side" ? "197px" : "120px";
      } else {
        return "auto";
      }
    },
    videoLayoutMode() {
      return this.$store.state.videoLayoutMode;
    },
    panelVisible() {
      return this.$store.state.panelVisible;
    },
    chatPanelVisible() {
      return this.$store.state.chatPanelVisible;
    },
    chatPanelClsed() {
      return this.$store.state.chatPanelClsed;
    },
    plistPanelClsed() {
      return this.$store.state.plistPanelClsed;
    },
    chatPanelPopouted() {
      return this.$store.state.chatPanelPopouted;
    },
    fixHeight() {
      return (
        !this.chatPanelClsed &&
        this.$store.state.plistPanelVisible &&
        !this.$store.state.plistPanelClsed &&
        !this.fullscreen
      );
    },
    peopleList() {
      return this.$store.state.peopleList;
    },
    chatMsgList() {
      return this.$store.state.chatMsgList;
    },
    rightPanelWidth() {
      return this.$store.state.rightPanelWidth;
    },
    plistPanelVisible() {
      return this.$store.state.plistPanelVisible;
    },
    fullscreen() {
      return this.$store.state.fullscreen;
    }
  },
  mounted() {
  },
  watch: {},
  methods: {
    close() {
      this.$store.commit("chatPanelVisible", false);
      setTimeout( _ => this.$store.commit("chatPanelPopouted", false), 100);
    },
    collapse() {
      this.$store.commit("chatPanelClsed", !this.chatPanelClsed);
    },
    popout() {
      if (!this.chatPanelPopouted) {
        this.$store.commit("chatPanelClsed", false);
      }
      this.$store.commit("chatPanelPopouted", !this.chatPanelPopouted);
    },
    sendmsg() {}
  }
};
</script>


