<script>

export default {

    data() {
        return {

        }
    },
    computed:{
        theme() {
          return this.$store.state.theme;
        },
        title () {
          if(this.popAVSettingsType === 'audio'){
            return 'Speaker and Microphone Settings';
          } else if(this.popAVSettingsType === 'video' || this.popAVSettingsType === 'vbg'){
            return 'Camera Settings';
          } else {
            return 'Speaker, Microphone and Camera';
          }
        },
        popAVSettingsType(){
            return this.$store.state.popAVSettingsType;
        },
        popAVSettingsVisible(){
            return this.$store.state.popAVSettingsVisible;
        },
        audioConnected(){
            return this.$store.state.audioConnected;
        },
        audioType(){
            return this.$store.state.audioType;
        },
        devicePaired(){
            return this.$store.state.devicePaired;
        },
        devicePairedName(){
            return this.$store.state.devicePairedName;
        },
        selectedSpeaker(){
            return this.$store.state.selectedSpeaker;
        },
        selectedMicrophone(){
            return this.$store.state.selectedMicrophone;
        },
        selectedCamera(){
            return this.$store.state.selectedCamera;
        },
    },
    mounted(){

    },
    watch: {
      popAVSettingsVisible(newVal) {
        if (newVal) {
          this.initCam();
        } else {
          this.stopCam();
        }
      }
    },
    methods:{
      initCam() {
        const selfViewVideo = document.getElementById('setting_preview_camera');
        const constraints = { audio: false, video: { width: 362, height: 223 } };
        navigator.mediaDevices
          .getUserMedia(constraints)
          .then(function(mediaStream) {
            const video = selfViewVideo;
            video.srcObject = mediaStream;
            video.onloadedmetadata = function(e) {
              video.play();
            };
          })
          .catch(function(err) {
            console.log(err.name + ': ' + err.message);
          });
      },

      stopCam() {
        const selfViewVideo = document.getElementById('setting_preview_camera');
        if (selfViewVideo) {
          selfViewVideo.pause();
        }
        
      },
      goTab(name) {
        this.$store.commit('popAVSettingsType', name); 
      },

      selectSpeaker(selected) {
        this.$store.commit("selectedSpeaker",selected[0].value);
      },
      selectMicrophone(selected) {
        this.$store.commit("selectedMicrophone", selected[0].value);
      },
      selectCamera(selected) {
        this.$store.commit("selectedCamera", selected[0].value);
      },
      
      close(evt){
          this.$store.commit('popAVSettingsVisible', false); 
      },
    }

}
</script>

