<template>
    <div class="wl-accordion" :class="[theme]">
        <div class='header' :class="{botborder: showBottomBorder}" @click.stop='toggleCollapse'>
          <slot name="header"></slot>
          <md-icon name="arrow-down_12" v-if="collapsed" />
          <md-icon name="arrow-up_12" v-if="!collapsed" />
        </div>
        <slot v-if="!collapsed"></slot>
    </div>
</template>

<script>


export default {
  props:{
      defaultCollapsed: {
          default: false,
      },
      showBottomBorder: {
          default: true,
      },
  },
  data() {
    return {
        collapsed: this.defaultCollapsed,
    }
  },
  computed:{
    theme() {
      return this.$store.state.theme;
    },
  },
  mounted(){

  },
  watch: {

  },
  methods:{
    toggleCollapse(){
      this.collapsed = !this.collapsed;
    },
  }
}
</script>


