<template>
    <div class='plistitm' :class="[theme, {sub: room}]">
        <div class='col col0' v-if="room"></div>
        <div class='col col1' 
            :class="{
                sub: room,
            }">
            <div
              :class="{
                avatar: type !== 'room',
                sub: room,
                room: type === 'room',
              }"
              :style='{backgroundImage: avatarUrl}' >
                <span v-if="!avatar && type !== 'room'">{{ abbr }}</span>
                <span v-if="type === 'room'" class="md-icon icon icon-endpoint_12" style="color:#707070"></span>
            </div>
            <span class="md-icon icon device-icon" :class="audioIco" v-if="!room"></span>
        </div>
        <div class='col3 lb_name' ><span class="txt">{{ name }} <span class='ico_host' v-if='host' /></span> <span class="suffix">{{ suffix }}</span></div>
        <div class='col col4'>
          <md-icon name="camera_12" v-if='video && !room' />
        </div>

        <div class='col col5' :class="{normal:!room}">
          <span class="md-icon icon icon-microphone-muted_12 md-color--red-50" v-if='muted2' ></span>
          
          <div class="ico_audio_last" v-if="!room && !muted2 && name === activeSpeakerName">
            <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M4 6H8V8C8 9.10457 7.10457 10 6 10C4.89543 10 4 9.10457 4 8V6Z" fill="#00CF64"/>
                <rect x="4" y="4" width="4" height="1" fill="#00CF64"/>
                <path fill-rule="evenodd" clip-rule="evenodd" d="M4.5 7.5V2.5C4.5 1.673 5.173 1 6 1C6.827 1 7.5 1.673 7.5 2.5V7.5C7.5 8.327 6.827 9 6 9C5.173 9 4.5 8.327 4.5 7.5ZM8.5 7.5V2.5C8.5 1.125 7.375 0 6 0C4.625 0 3.5 1.125 3.5 2.5V7.5C3.5 8.704 4.362 9.716 5.5 9.949V11H5.501H4.5C4.225 11 4 11.226 4 11.5C4 11.775 4.225 12 4.5 12H7.5C7.775 12 8 11.775 8 11.5C8 11.226 7.775 11 7.5 11H6.5V9.949C7.638 9.716 8.5 8.704 8.5 7.5Z" fill="#00CF64"/>
            </svg>
          </div>
        </div>

        <div class='col col5' :class="{hover:!room}" v-if="!room && audio !== 'none'">
          <div class="unmuted" v-if="!muted2"><span class="md-icon icon icon-microphone-muted_12" @click="mute"></span></div>
          <div class="muted" v-if="muted2"><span class="md-icon icon icon-microphone-muted_12 md-color--white" @click="unmute"></span></div>
        </div>
    </div>
</template>

<script>


export default {
    props:[
        'type',
        'name',
        'me',
        'host',
        'avatar',
        'video',
        'audio',
        'muted',
        'room',
    ],
    data() {
        return {
            muted2: this.muted,
        }
    },
    computed:{
      theme() {
        return this.$store.state.theme;
      },
      activeSpeakerName(){
          return this.$store.state.activeSpeakerName;
      },
      suffix(){
          let arr = [];
          if(this.me){
              arr.push('Me');
          }
          if(this.host){
              arr.push('Host');
          }
          if(arr.length > 0){
              return arr.join(', ');
          }
          return '';
      },
      avatarUrl(){
          if(this.avatar){
              return 'url('+this.$store.state.avatarPath+this.avatar+')';
          }else{
              return 'none';
          }
      },
      abbr(){
          let arr = this.name.split(' ');
          return arr[0][0].toUpperCase() + arr[1][0].toUpperCase();
      },
      audioIco(){
          if(this.audio == 'mobile'){
              return 'ico_mobile';
          }else if(this.audio == 'pc'){
              return 'ico_headset';
          }else if(this.audio == 'phone'){
              return 'ico_headset';
          }else{
              return '';
          }
      },
      peopleList() {
        return this.$store.state.peopleList;
      },
    },
    mounted(){

    },
    watch: {
        muted(val) {
            this.muted2 = val;
        }
    },
    methods:{
        mute() {
            if (this.type === 'room') {
                this.muted2 = true;
            } else {
                let plist = [];
                const len = this.peopleList.length;
                for (let i = 0; i < len; i++) {
                    const p = this.peopleList[i];
                    if (p.name === this.name) {
                        p.muted = true;
                    }
                    plist.push(p);
                }
                this.$store.commit("peopleList", plist);
            }
        },
        unmute() {
            if (this.type === 'room') {
                this.muted2 = false;
            } else {
                let plist = [];
                const len = this.peopleList.length;
                for (let i = 0; i < len; i++) {
                    const p = this.peopleList[i];
                    if (p.name === this.name) {
                        p.muted = false;
                    }
                    plist.push(p);
                }
                this.$store.commit("peopleList", plist);
            }
            
        },
    }
}
</script>


