<template>
    <div
    id="ctrl-menu-video"
    ref="popover"
    v-if="ctrlMenuVideoVisible"
    :class="[theme]"
    :style="{
      left: (popStartPoint[0] - 112) + 'px',
      top: (popStartPoint[1] - 6) + 'px',
    }"
    >

        <div class='wrap'>
          <div class='grp' >
            <div class='tt split-line'>
              <md-icon name="camera_12" :color="theme === 'dark' ? 'gray-40' : 'gray-60'" />
              <span>Camera</span>
            </div>
            <div class='itm' @click="selectCamera('Internal Camera')">
              <md-icon class="ico" name="check_12" :color="theme === 'dark' ? 'blue-30' : 'blue-50'" v-if="selectedCamera === 'Internal Camera'" />
              <span class="lb indent" >Internal Camera</span>
            </div>

            <div class='itm split-line' @click="selectCamera('Logitech Webcam')">
              <md-icon class="ico" name="check_12" :color="theme === 'dark' ? 'blue-30' : 'blue-50'" v-if="selectedCamera === 'Logitech Webcam'" />
              <span class="lb indent" >Logitech Webcam</span>
            </div>

          </div>


          <div class='grp' >
            <div class='itm split-line' @click="showPopSettings('video')">
              <md-icon class="ico" name="settings_12" />
              <span class="lb indent" >Settings...</span>
            </div>
          </div>

          <div class='grp' >
            <div class='itm ' @click="showPopSettings('vbg')">
              <md-icon class="ico" name="video-effect_12"  />
              <span class="lb indent" >Change Virtual Background...</span>
            </div>
          </div>



        </div>
    </div>
</template>

<script>
import hitTest from "../utils/hitTest";

export default {
  data() {
    return {
      
    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    ctrlMenuVideoVisible() {
      return this.$store.state.ctrlMenuVideoVisible;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },

    selectedCamera(){
        return this.$store.state.selectedCamera;
    },
    
  },
  mounted() {},
  watch: {
    ctrlMenuVideoVisible(newVal) {
      if (newVal) {
        window.addEventListener("mousedown", this.onMouseDown);
      } else {
        window.removeEventListener("mousedown", this.onMouseDown);
      }
    }
  },
  methods: {
    onMouseDown(evt) {
      if (
        this.$refs.popover &&

        !hitTest(this.$refs.popover, evt.pageX, evt.pageY)
      ) {
        this.close();
      }
    },
    showPopSettings(type) {
      this.$store.commit("popAVSettingsType", type);
      this.$store.commit("popAVSettingsVisible", true);
      this.close();
    },

    selectCamera(name) {
      this.$store.commit("selectedCamera", name);
      this.close();
    },
    
    close() {
      this.$store.commit("ctrlMenuVideoVisible", false);
    },
  }
};
</script>

