<template>
	<div id='pop-audio-option' 
        class="pop-audio-option"
        :class="[theme]"
        v-show='interstitialAudioOptionVisible'
    >
        <div class='wrap'>
            
            <div class='tt'>
                Connect to audio using
            </div>

            <div class='itm itm_pc ico_pc' :class='{selected: audioType=="pc"}' v-on:click='selectItem("pc")'>
                <md-icon class="ico" name="headset_16" />
                <span class="lb">Computer</span>
                <span class="ico_selected" v-show='audioType=="pc"'><md-icon name="check-circle-active_16" color="blue" /></span>
            </div>
            
            <div class='itm itm_callme ico_callme exp' :class='{selected: audioType=="callme"}' v-on:click='selectItem("callme")'>
                <div class="col">
                    <div class="rw">
                        <md-icon class="ico" name="incoming-call-legacy_16" />
                        <span class="lb">Call Me</span>

                        <div class='itm_callme_expand'>
                            <span class="combobox"><span class="clb">+1 408-123-4567</span> <md-icon name='arrow-down_12' class="arrow" /></span>
                        </div>
                        <span class="ico_selected" v-show='audioType=="callme"'><md-icon name="check-circle-active_16" color="blue" /></span>
                    </div>
                    <div class="checkbox">
                        <md-icon name='placeholder_16' color="gray-30" style="margin-right: 5px;" />
                        Connect to audio without pressing <b>&nbsp;1&nbsp;</b> my phone
                    </div>
                </div>
            </div>

            <div class='itm itm_callin ico_callin' :class='{selected: audioType=="callin"}' v-on:click='selectItem("callin")'>
                <md-icon class="ico" name="outgoing-call-legacy_16" />
                <span class="lb">Call In</span>
                <span class="ico_selected" v-show='audioType=="callin"'><md-icon name="check-circle-active_16" color="blue" /></span>
            </div>

            <div class='itm itm_none ico_none' :class='{selected: audioType=="none"}' v-on:click='selectItem("none")'>
                <md-icon class="ico" name="blocked_16" />
                <span class="lb">Don't connect to audio</span>
                <span class="ico_selected" v-show='audioType=="none"'><md-icon name="check-circle-active_16" color="blue" /></span>
            </div>

        </div>
        
    </div>
</template>

<script>

import hitTest from '../utils/hitTest'
import {addClass, hasClass, removeClass, style, height } from 'dom-helpers';

export default {
    data() {
        return {
        }
    },
    computed:{
        theme() {
            return this.$store.state.theme;
        },
        audioConnected(){
            return this.$store.state.audioConnected;
        },
        interstitialAudioOptionVisible(){
            return this.$store.state.interstitialAudioOptionVisible;
        },
        popStartPoint(){
            return this.$store.state.popStartPoint;
        },
        audioType(){
            return this.$store.state.audioType;
        },
        
    },
    mounted(){
        window.addEventListener('mousedown', this.onMouseDown);
    },
    watch: {
    },
    methods:{
        onMouseDown(evt){
            if( document.querySelector('#pop-audio-option .wrap') && !hitTest(document.querySelector('#pop-audio-option .wrap'), evt.pageX, evt.pageY) ){
                this.close();
            }
        },
        selectItem(type){
            this.$store.commit('audioType', type);
            if(type !== 'callme') {
                this.close();
            }
        },
        close(){
            this.$store.commit('interstitialAudioOptionVisible', false);
        },
    },
    beforeDestroy(){
        window.removeEventListener('mousedown', this.onMouseDown);
    },
    

}
</script>

