<template>
    <div
      id="pop_share"
      ref="popover"
      v-if="popShareVisible"
      :class="[theme]"
      :style="{
        left: popStartPoint[0] + 'px',
        top: (popStartPoint[1] - 20) + 'px',
      }"
    >
      <div class="triangle"></div>
      <div class='close' @click='close' >
        <span class="md-icon icon icon-cancel_12"></span>
      </div>
      <div class="wrap">
        <div class="title">Share Content</div>

        <div class="row">
          <div class="itm screen1">
            <div class="thumb"></div>
            <div class="msk">
              <div class="btn">Share</div>
            </div>
            <div class="lb">Screen 1</div>
          </div>

          <div class="itm screen2">
            <div class="thumb"></div>
            <div class="msk">
              <div class="btn">Share</div>
            </div>
            <div class="lb">Screen 2</div>
          </div>
        </div>
        <div class="spline"></div>
        <div class="row">
          <div class="itm app1">
            <div class="thumb"></div>
            <div class="msk">
              <div class="btn">Share</div>
            </div>
            <div class="lb">PowerPoint</div>
          </div>

          <div class="itm app2">
            <div class="thumb"></div>
            <div class="msk">
              <div class="btn">Share</div>
            </div>
            <div class="lb">Sketch</div>
          </div>

          <div class="itm app3">
            <div class="thumb"></div>
            <div class="msk">
              <div class="btn">Share</div>
            </div>
            <div class="lb">Acrobat Reader</div>
          </div>
        </div>
        <div class="spline"></div>
        <div class="row">
          <div class="itm type1">
            <div class="thumb">
              <span class="md-icon icon icon-files_24" ></span>
            </div>
            <div class="lb">Share File</div>
          </div>

          <div class="itm type2">
            <div class="thumb">
              <span class="md-icon icon icon-applications_24" ></span>
            </div>
            <div class="lb">Other Applications</div>
          </div>

          <div class="itm type3">
            <div class="thumb">
              <span class="md-icon icon icon-plus_24" ></span>
            </div>
            <div class="lb">New Whiteboard</div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import hitTest from "../utils/hitTest";

export default {
  data() {
    return {};
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    popShareVisible() {
      return this.$store.state.popShareVisible;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    }
  },
  mounted() {},
  watch: {
    popShareVisible: function(newVal) {
      if (newVal) {
        window.addEventListener("mousedown", this.onMouseDown);
      } else {
        window.removeEventListener("mousedown", this.onMouseDown);
      }
    }
  },
  methods: {
    onMouseDown(evt) {
      if (
        this.$refs.popover &&
        !hitTest(
          document.querySelector("#controls .btn_share"),
          evt.pageX,
          evt.pageY
        ) &&
        !hitTest(this.$refs.popover, evt.pageX, evt.pageY)
      ) {
        this.$store.commit("popShareVisible", false);
      }
    },
    close(evt) {
      this.$store.commit("popShareVisible", false);
    }
  }
};
</script>

