<script>

import hitTest from '../utils/hitTest'
import {addClass, hasClass, removeClass, style, height } from 'dom-helpers';

export default {
    data() {
        return {
          name:'Cisco Webex Meetings',

          audio_option_label:'Use Computer for audio',
          callmenumber:'Call me at ',
          isVideoAddress:false,
          deviceMenuVisible:false,
          currentSettingVisible:false,
          notification: null,
          showNotification: false,
          itv_notification: 0,
          camMenuVisible: false,
        }
    },
    computed:{
        theme() {
          return this.$store.state.theme;
        },
        controlOffRedStyle(){
          return this.$store.state.controlOffRedStyle;
        },
        peopleList(){
          return this.$store.state.peopleList;
        },
        windowsSize(){
          return this.$store.state.windowsSize;
        },
        windowsWidth(){
          return this.$store.state.windowsWidth;
        },
        windowsHeight(){
          return this.$store.state.windowsHeight;
        },
        interstitialCamOn(){
        	return this.$store.state.interstitialCamOn;
        },
        selectedSpeaker(){
        	return this.$store.state.selectedSpeaker;
        },
        selectedMicrophone(){
        	return this.$store.state.selectedMicrophone;
        },
        selectedCamera(){
        	return this.$store.state.selectedCamera;
        },
        audioType(){
        	return this.$store.state.audioType;
        },
        inMeeting(){
        	return this.$store.state.inMeeting;
        },
        meetingTitle(){
            return this.$store.state.meetingTitle;
        },        
        muted(){
            return this.$store.state.muted;
        },
        devicePaired(){
            return this.$store.state.devicePaired;
        },
        devicePairedName(){
            return this.$store.state.devicePairedName;
        },
        avatarPath(){
            return this.$store.state.avatarPath;
        },
        me(){
            return this.$store.getters.me;
        },
    },
    mounted() {
      this.initCam();

      window.addEventListener('mousedown', this.onMouseDown);


      this.notification = 'Microphone is ' + (this.muted ? 'off' : 'on') + ', camera is ' + (this.interstitialCamOn ? 'on' : 'off');
      this.showNotification = true;
      clearTimeout(this.itv_notification);
      this.itv_notification = setTimeout(() => {
        this.showNotification = false;
      }, 3000);

    },
    watch: {
      audioType(newVal) {
        if(newVal == 'pc'){
            this.audio_option_label = 'Use Computer for audio';
            this.marquee_paused = false;
        }else if(newVal == 'callme'){
            this.audio_option_label = '';
            this.marquee_paused = true;
        }else if(newVal == 'callin'){
            this.audio_option_label = 'Call in';
            this.marquee_paused = true;
        }else if(newVal == 'none'){
            this.audio_option_label = "Don't connect to audio";
            this.marquee_paused = true;
        }
      },
    },
    methods: {
      initCam() {
        const selfViewVideo = document.getElementById('camera_interstital');
        // Prefer camera resolution nearest to 1280x720.
        const constraints = { audio: false, video: { width: 1280, height: 720 } };
        navigator.mediaDevices
          .getUserMedia(constraints)
          .then(function(mediaStream) {
            const video = selfViewVideo;
            video.srcObject = mediaStream;
            video.onloadedmetadata = function(e) {
              video.play();
            };
          })
          .catch(function(err) {
            console.log(err.name + ': ' + err.message);
          });
      },
      joinMeeting(evt) {
        this.$store.commit('meetingVisible', true);
        this.$store.commit('interstitialVisible', false);
      },
      close(evt) {
        this.$store.commit('interstitialVisible', false);
      },
      
      toggleVideo(evt) {
        const camon = !this.interstitialCamOn;
        this.$store.commit('interstitialCamOn', camon);
        this.$store.commit('cameraOn', camon);

        let plist = [];
        const len = this.peopleList.length;
        for (let i = 0; i < len; i++) {
            const p = this.peopleList[i];
            if (p.me) {
                p.video = camon ? 'camera' : null;
            }
            plist.push(p);
        }
        this.$store.commit("peopleList", plist);

        if(this.interstitialCamOn){
          this.notification = 'Your video is on';
        }else{
          this.notification = 'Your video is off';
        }
        this.showNotification = true;
        clearTimeout(this.itv_notification);
        this.itv_notification = setTimeout(() => {
          this.showNotification = false;
        }, 3000);

      },
      toggleMute(evt) {
        const mut = this.muted;
        this.$store.commit('muted', !mut);

        let plist = [];
        const len = this.peopleList.length;
        for (let i = 0; i < len; i++) {
            const p = this.peopleList[i];
            if (p.me) {
                p.muted = !mut;
            }
            plist.push(p);
        }
        this.$store.commit("peopleList", plist);

        if(!mut){
          this.notification = 'You\'re unmuted';
        }else{
          this.notification = 'You\'re muted';
        }
        this.showNotification = true;
        clearTimeout(this.itv_notification);
        this.itv_notification = setTimeout(() => {
          this.showNotification = false;
        }, 3000);

      },
      showAudioOption(evt) {
         this.$store.commit('interstitialAudioOptionVisible', true);
      },
      showAvSetting(evt) {
        this.$store.commit("popAVSettingsType", 'audio');
        this.$store.commit("popAVSettingsVisible", true);
      },
      showPopSettings(type) {
        this.$store.commit("popAVSettingsType", type);
        this.$store.commit("popAVSettingsVisible", true);
      },
      showCurrentSetting(evt) {
         this.currentSettingVisible = true;
      },
      hideCurrentSetting(evt) {
         this.currentSettingVisible = false;
      },
      onMouseDown(evt){
        if( this.$refs.device_menu && !hitTest(this.$refs.device_menu, evt.pageX, evt.pageY) ){
          this.deviceMenuVisible = false;
        }
        if( this.$refs.cam_menu && !hitTest(this.$refs.cam_menu, evt.pageX, evt.pageY) ){
          this.camMenuVisible = false;
        }
      },

      searchDevice(evt){
        //$('.page_main').hide();
        //$('.page_search').show();
        //$('.device_menu .head .backbtn').show();
      },
      closeSearchDevice(evt){
        //$('.page_main').show();
        //$('.page_search').hide();
        //$('.device_menu .head .backbtn').hide();
      },


      showDeviceMenu(evt) {
        this.deviceMenuVisible = true;
      },
      selectDevice(name, videoaddress=false) {
        return;





        this.isVideoAddress = videoaddress;
        this.$store.commit('devicePaired', true);
        this.$store.commit('devicePairedName', name);
        this.closeSearchDevice();
        this.deviceMenuVisible = false;
        // this.close();
      },
      disconnectDevice(evt) {
        this.$store.commit('devicePaired', false);
        this.$store.commit('devicePairedName', null);
        this.deviceMenuVisible = false;
      },

      showCamMenu() {
        this.camMenuVisible = !this.camMenuVisible;
      },
      selectCamera(name) {
        this.$store.commit("selectedCamera", name);
        this.camMenuVisible = false;
      },
      
    },
    
}
</script>

