<template>
    <div
    id="ctrl-menu-aduio"
    ref="popover"
    v-if="ctrlMenuAudioVisible"
    :class="[theme]"
    :style="{
      left: (popStartPoint[0] - 145) + 'px',
      top: (popStartPoint[1] - 6) + 'px',
    }"
    >

        <div class='wrap'>
          <div class='grp' v-if="audioType === 'pc'" >
            <div class='tt split-line'>
              <md-icon name="speaker_12" :color="theme === 'dark' ? 'gray-40' : 'gray-60'" />
              <span>Speaker</span>
            </div>
            <div class='itm' @click="selectSpeaker('Use System Setting (Internal Speakers)')">
              <md-icon class="ico" name="check_12" :color="theme === 'dark' ? 'blue-30' : 'blue-50'" v-if="selectedSpeaker === 'Use System Setting (Internal Speakers)'" />
              <span class="lb indent" >Use System Setting (Internal Speakers)</span>
            </div>

            <div class='itm' @click="selectSpeaker('Internal Speakers')">
              <md-icon class="ico" name="check_12" :color="theme === 'dark' ? 'blue-30' : 'blue-50'" v-if="selectedSpeaker === 'Internal Speakers'" />
              <span class="lb indent" >Internal Speakers</span>
            </div>

            <div class='itm' @click="selectSpeaker('Bose Headset 100')">
              <md-icon class="ico" name="check_12" :color="theme === 'dark' ? 'blue-30' : 'blue-50'" v-if="selectedSpeaker === 'Bose Headset 100'" />
              <span class="lb indent" >Bose Headset 100</span>
            </div>

          </div>


          <div class='grp' v-if="audioType === 'pc'" >
            <div class='tt split-line'>
              <md-icon name="microphone_12" :color="theme === 'dark' ? 'gray-40' : 'gray-60'" />
              <span>Microphone</span>
            </div>
            <div class='itm' @click="selectMicrophone('Use System Setting (Internal Microphone)')">
              <md-icon class="ico" name="check_12" :color="theme === 'dark' ? 'blue-30' : 'blue-50'" v-if="selectedMicrophone === 'Use System Setting (Internal Microphone)'" />
              <span class="lb indent" >Use System Setting (Internal Microphone)</span>
            </div>

            <div class='itm' @click="selectMicrophone('Internal Microphone')">
              <md-icon class="ico" name="check_12" :color="theme === 'dark' ? 'blue-30' : 'blue-50'" v-if="selectedMicrophone === 'Internal Microphone'" />
              <span class="lb indent" >Internal Microphone</span>
            </div>

            <div class='itm split-line' @click="selectMicrophone('Bose Headset 100')">
              <md-icon class="ico" name="check_12" :color="theme === 'dark' ? 'blue-30' : 'blue-50'" v-if="selectedMicrophone === 'Bose Headset 100'" />
              <span class="lb indent" >Bose Headset 100</span>
            </div>

          </div>

          <div class='grp' v-if="audioType === 'pc'" >
            <div class='itm  split-line' @click="showPopSettings">
              <md-icon class="ico" name="settings_12" />
              <span class="lb indent" >Settings...</span>
            </div>
          </div>

          <div class='grp' v-if="audioType">
            <div class='tt' style="margin-bottom: -2px; margin-left: -6px;">
              <span v-if="audioType === 'pc'">You are using computer for audio</span> 
              <span v-if="audioType === 'callme'">We’re calling you at +1 408-123-4567</span> 
              <span v-if="audioType === 'callin'">You’re calling in</span> 
              <span v-if="audioType === 'none'">You’re not connected to audio</span> 
            </div>

            <div class='itm ' @click="showPopAudioOptions">
              <md-icon class="ico" name="handset_12" />
              <span class="lb indent" >Change Audio Connection...</span>
            </div>
          </div>


        </div>
    </div>
</template>

<script>
import hitTest from "../utils/hitTest";

export default {
  data() {
    return {
      
    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    ctrlMenuAudioVisible() {
      return this.$store.state.ctrlMenuAudioVisible;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },
    selectedSpeaker(){
        return this.$store.state.selectedSpeaker;
    },
    selectedMicrophone(){
        return this.$store.state.selectedMicrophone;
    },
    audioType(){
        return this.$store.state.audioType;
    },

  },
  mounted() {},
  watch: {
    ctrlMenuAudioVisible(newVal) {
      if (newVal) {
        window.addEventListener("mousedown", this.onMouseDown);
      } else {
        window.removeEventListener("mousedown", this.onMouseDown);
      }
    }
  },
  methods: {
    onMouseDown(evt) {
      if (
        this.$refs.popover &&
        
        !hitTest(this.$refs.popover, evt.pageX, evt.pageY)
      ) {
        this.close();
      }
    },
    
    showPopSettings() {
      this.$store.commit("popAVSettingsType", 'audio');
      this.$store.commit("popAVSettingsVisible", true);
      this.close();
    },
    selectSpeaker(name) {
      this.$store.commit("selectedSpeaker", name);
      this.close();
    },
    selectMicrophone(name) {
      this.$store.commit("selectedMicrophone", name);
      this.close();
    },
    showPopAudioOptions() {
      this.close();
      this.$store.commit("popAudioOptionsVisible", true);
    },
    close() {
      this.$store.commit("ctrlMenuAudioVisible", false);
    },
  }
};
</script>

