<template>
  <div id="controls_wrap" ref="controlwrap" :class="[theme]" >

    <div v-if="size==='normal'" class='group_btn group_btn_left'>

      <div class='gbtn' >
        <div id="ctrl-ai-button" :class="{'ai-off': !transcriptEnabled, ai: transcriptEnabled}" @mouseover="showCCBubble" >
          <md-icon name="closed-caption_16" class="cc" v-if="!transcriptEnabled"></md-icon>
        </div>
      </div>

      <div class='gbtn' @click="toggleCC" :class="{txtselected: closedCaptionEnabled}" v-if="transcriptEnabled" v-tooltip="'Closed Caption'">
        <md-icon name="closed-caption_16" style="width:18px; padding-left:1px;"></md-icon>
      </div>

    </div><!--group_btn_left-->

    <div id="controls" :class="{bg: showBackground && size==='mini'}">

      <div
        class="btn btn_audio"
        v-if="audioType === 'none' && !devicePaired"
        v-tooltip="'Audio'"
        :class="{mini: size==='mini', 'ico-with-lb': size==='normal'}"
        @click="showPopAudioOptions"
      >
        <md-icon class="ico" :name="size==='mini' ? 'handset_12' : 'handset_20' " />
        <span v-if="size==='normal'" class="lb">Audio</span>
      </div>
      
      <div
        class="btn btn_mute"
        v-if="audioType !== 'none' || devicePaired"
        @click="toggleMute"
        :class="{
          off: controlOffRedStyle === 1 && muted, 
          mini: size==='mini', 'ico-with-lb': size==='normal',
          nostate: hoverSplitted,
        }"
      >
        <span class="lft" v-tooltip="muted ? 'Unmute my microphone' : 'Mute my microphone'">
        <div class="ico ico_audio" :class="{scaledown: size==='mini'}">
          <svg v-show="!muted" width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
              <defs>
                  <clipPath id="clipOut">
                      <path fill-rule="nonzero" d="M14 13.5C14 15.43 12.43 17 10.5 17C8.57 17 7 15.43 7 13.5V5.5C7 3.57 8.57 2 10.5 2C12.43 2 14 3.57 14 5.5V13.5Z" :fill="theme === 'light' ? '#171B1F' : '#FFFFFF'"></path>
                  </clipPath>
                  <clipPath id="clipInner">
                      <path d="M6.5 11H14.5V14C14.5 16.2091 12.7091 18 10.5 18C8.29086 18 6.5 16.2091 6.5 14V11Z" fill="#44CF50"></path>
                      <rect x="6.5" y="9" width="8" height="1" fill="#44CF50"></rect>
                      <rect x="6.5" y="7" width="8" height="1" fill="#44CF50"></rect>
                      <rect x="6.5" y="5" width="8" height="1" fill="#44CF50"></rect>
                      <path d="M7 4C7 3.44772 7.44772 3 8 3H13C13.5523 3 14 3.44772 14 4H7Z" fill="#44CF50"></path>
                  </clipPath>
              </defs>
              <g clip-path="url(#clipOut)">
                  <rect id="aduioIconRectBackground" clip-path="url(#clipInner)" x="7" y="10.778106508875739" width="7" height="15" fill="#44CF50"></rect>
              </g>
              <path fill-rule="nonzero" d="M14 13.5C14 15.43 12.43 17 10.5 17C8.57 17 7 15.43 7 13.5V5.5C7 3.57 8.57 2 10.5 2C12.43 2 14 3.57 14 5.5V13.5ZM6 5.5V13.5C6 15.815 7.755 17.699 10.003 17.95C10.003 17.956 10 20 10 20H8C7.725 20 7.5 20.224 7.5 20.5C7.5 20.776 7.725 21 8 21H13C13.276 21 13.5 20.776 13.5 20.5C13.5 20.224 13.276 20 13 20H11V17.967C13.245 17.699 15 15.815 15 13.5V5.5C15 3.015 12.985 1 10.5 1C8.015 1 6 3.015 6 5.5Z" :fill="theme === 'light' ? '#171B1F' : '#FFFFFF'"></path>
          </svg>
          
          <md-icon name="microphone-muted_20" :color="!controlOffRedStyle ? (theme !== 'light' ? 'red-50' : 'red-60') : 'white'" v-if="muted" />
        </div>
        <span v-if="!controlOffRedStyle && size==='normal'" class="lb" >{{muted ? 'Unmute' : 'Mute'}}</span>
        <span v-if="controlOffRedStyle === 1 && size==='normal'" class="lb" style="width: 23px">{{muted ? 'Off' : 'On'}}</span>
        </span>
        <div 
          class="splitted btn_audio2" 
          :class="{active: ctrlMenuAudioVisible}"
          v-if="size !== 'mini' && (audioType === 'pc' || audioType === 'callme' || audioType === 'callin') " 
          @click="showAudioOptionMenu" 
          @mouseenter="_ => hoverSplitted = true"
          @mouseleave="_ => hoverSplitted = false"
        >
          <md-icon name="arrow-down_8" />
        </div>
      </div>

      <div
        class="btn btn_video"
        @click="toggleVideo"
        :class="{
          off: controlOffRedStyle === 1 && !cameraOn, 
          mini: size==='mini', 'ico-with-lb': size==='normal',
          nostate: hoverSplitted,
        }"
      >
        <span class="lft" v-tooltip="!cameraOn ? 'Turn on my video' : 'Turn off my video'">
          <svg v-if="cameraOn" class="ico" :class="{scaledown: size==='mini'}" width="20px" height="14px" viewBox="0 0 20 14" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
              <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                  <g id="activities/camera_20" transform="translate(0.000000, -3.000000)" :fill="theme === 'light' ? '#171B1F' : '#FFFFFF'">
                      <g id="camera_20" transform="translate(0.000000, 3.000000)">
                          <path d="M19.004,10.855 C19.004,10.936 18.957,10.972 18.917,10.989 C18.868,11.011 18.817,11.001 18.776,10.959 L15,7.127 L15,6.872 L18.776,3.041 C18.817,2.999 18.867,2.987 18.917,3.01 C18.957,3.027 19.004,3.063 19.004,3.144 L19.004,10.855 Z M14,11.497 C14,12.323 13.323,13 12.506,13 L2.493,13 C1.67,13 1,12.327 1,11.497 L1,2.503 C1,1.677 1.676,1 2.493,1 L12.506,1 C13.33,1 14,1.673 14,2.503 L14,11.497 Z M19.296,2.085 C18.871,1.91 18.39,2.008 18.068,2.337 L15,5.451 L15,2.503 C15,1.122 13.883,0 12.506,0 L2.493,0 C1.127,0 0,1.121 0,2.503 L0,11.497 C0,12.878 1.116,14 2.493,14 L12.506,14 C13.872,14 15,12.879 15,11.497 L15,8.548 L18.068,11.662 C18.284,11.882 18.573,11.999 18.867,11.999 C19.012,11.999 19.156,11.971 19.296,11.914 C19.724,11.738 20,11.322 20,10.855 L20,3.144 C20,2.677 19.724,2.261 19.296,2.085 L19.296,2.085 Z" id="fill"></path>
                      </g>
                  </g>
                  <circle id="Oval" fill="#44CF50" cx="5" cy="5" r="2" v-if="cameraOn" class=""></circle>
              </g>
          </svg>
          <md-icon :name="size==='mini' ? 'camera-muted_12' : 'camera-muted_20'" class="ico" :color="!controlOffRedStyle ? (theme !== 'light' ? 'red-50' : 'red-60') : 'white'" v-if="!cameraOn" />
          <span v-if="!controlOffRedStyle && size==='normal'" class="lb" >{{!cameraOn ? 'Start Video' : 'Stop Video'}}</span>
          <span v-if="controlOffRedStyle === 1 && size==='normal'" class="lb" style="width: 23px">{{!cameraOn ? 'Off' : 'On'}}</span>
        </span>
        <div 
          class="splitted btn_video2" 
          :class="{active: ctrlMenuVideoVisible}"
          v-if="size !== 'mini' && !devicePaired " 
          @click="showVideoOptionMenu" 
          @mouseenter="_ => hoverSplitted = true"
          @mouseleave="_ => hoverSplitted = false"
        >
          <md-icon name="arrow-down_8" />
        </div>
      </div>

      <div
        class="btn btn_volume"
        v-if="devicePaired"
        v-tooltip="'Volume'"
        @click="toggleVolume"
        :class="{mini: size==='mini', 'ico-with-lb': size==='normal'}"
      >
        <md-icon class="ico" :name="size==='mini' ? 'speaker_12' : 'speaker_20' " />
        <span v-if="size==='normal'" class="lb">Volume</span>
      </div>

      <div
        class="btn btn_share"
        v-tooltip="'Share Content'"
        @click="toggleShare"
        :class="{
          mini: size==='mini', 
          'ico-with-lb': size==='normal',
          active: popShareVisible,
        }"
      >
        <md-icon class="ico" :name="size==='mini' ? 'content-share_12' : 'content-share_20' " />
        <span v-if="size==='normal'" class="lb">Share</span>
      </div>

      <div
        class="btn btn_record"
        v-if="me.host"
        v-tooltip="'Record'"
        @click="toggleRecord"
        :class="{mini: size==='mini', 'ico-with-lb': size==='normal'}"
      >
        <span :class="{scaledown: size==='mini'}" class="ico" >
          <svg width="20px" height="20px" viewBox="0 0 20 20" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
              <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                  <path d="M10,1 C5.038,1 1,5.038 1,10 C1,14.962 5.038,19 10,19 C14.962,19 19,14.962 19,10 C19,5.038 14.962,1 10,1 M10,20 C4.486,20 0,15.514 0,10 C0,4.486 4.486,0 10,0 C15.514,0 20,4.486 20,10 C20,15.514 15.514,20 10,20" id="fill" :fill="theme === 'light' ? '#000000' : '#FFFFFF'"></path>
                  <circle stroke-width="1" :stroke="theme === 'light' ? '#000000' : '#FFFFFF'" cx="10" cy="10" r="4" v-if="!recordOn"></circle>
                  <circle cx="10" cy="10" r="4" fill="#D93829" id="recording-red-dot" v-show="recordOn"></circle>
              </g>
          </svg>
        </span>
        <span v-if="size==='normal'" class="lb">Record</span>
      </div>

      <div 
        v-if="size==='normal'" 
        class="btn btn_more" 
        :class="{
          active: popmenuMoreVisible,
        }"
        v-tooltip="'More Options'" 
        @click="togglePopmenuMore"
      >
        <span class="md-icon icon icon-more_20"></span>
      </div>
      <div class="btn btn_x" ref="btnleave" v-tooltip="'Leave'" :class="{mini: size==='mini'}">
        <md-icon :name="size==='mini' ? 'cancel_12' : 'cancel_20'" color="white" />
      </div>


    </div>

    <div v-if="size==='normal'" ref="rightctrl" class='group_btn group_btn_right'>

      <div class='gbtn' @click="togglePlist" :class="{txtselected: plistPanelVisible}" >
        <md-icon class="icon" name="participant-list_16" style="width:18px; padding-left:1px;"></md-icon>
        <span class="tt" v-if="showLabel">Participants</span>
      </div>

      <div class='gbtn' @click="toggleChat" :class="{txtselected: chatPanelVisible}">
        <md-icon class="icon" name="chat_16" style="width:18px; padding-left:1px;"></md-icon>
        <span class="tt" v-if="showLabel">Chat</span>
      </div>

      <div class='gbtn btn_panels' @click="toggleMore" :class="{active: popPanelVisible}">
        <md-icon name="more_16"></md-icon>
      </div>

    </div><!--group_btn_right-->

    <pop-ai-bubble v-if="ccBubbleVisible" @close="() => ccBubbleVisible = false"></pop-ai-bubble>
  </div>
</template>

<script>
import { Fragment } from 'vue-fragment'
import mframe from 'mframe'

export default {
  components: { Fragment },
  props: {
    size: {
      type: String,
      default: 'normal'
    },
    showBackground: {
      type: Boolean,
      default: true
    },
  },
  data() {
    return {
      annoToolsVisible: false,
      ccBubbleVisible: false,
      showLabel: false,
      hoverSplitted: false,
    };
  },
  computed: {
    controlOffRedStyle(){
      return this.$store.state.controlOffRedStyle
    },
    windowsSize(){
      return this.$store.state.windowsSize
    },
    host(){
      return this.$store.getters.host
    },
    me(){
      return this.$store.getters.me
    },
    recordOn() {
      return this.$store.state.recordOn;
    },
    theme() {
      return this.$store.state.theme;
    },
    sidePanelIsOpen() {
      return this.$store.state.sidePanelIsOpen;
    },
    videoLayoutMode() {
      return this.$store.state.videoLayoutMode;
    },
    audioConnected() {
      return this.$store.state.audioConnected;
    },
    videoConnected() {
      return this.$store.state.videoConnected;
    },
    muted() {
      return this.$store.state.muted;
    },
    cameraOn() {
      return this.$store.state.cameraOn;
    },
    fullscreen() {
      return this.$store.state.fullscreen;
    },

    currentSharing() {
      return this.$store.state.currentSharing;
    },

    anyOpenPanel() {
      return this.$store.getters.plistPanelVisible;
    },
    plistPanelVisible() {
      return this.$store.state.plistPanelVisible;
    },
    chatPanelVisible() {
      return this.$store.state.chatPanelVisible;
    },
    ccPanelVisible() {
      return this.$store.state.ccPanelVisible;
    },

    plistPanelPopouted() {
      return this.$store.state.plistPanelPopouted;
    },
    chatPanelPopouted() {
      return this.$store.state.chatPanelPopouted;
    },
    ccPanelPopouted() {
      return this.$store.state.ccPanelPopouted;
    },

    rightPanelWidth() {
      return this.$store.state.rightPanelWidth;
    },
    rightPanelContentWidth() {
      return this.$store.state.rightPanelContentWidth;
    },
    chatMsgList() {
      return this.$store.state.chatMsgList;
    },
    peopleList() {
      return this.$store.state.peopleList;
    },
    popmenuMoreVisible() {
      return this.$store.state.popmenuMoreVisible;
    },
    popPanelVisible() {
      return this.$store.state.popPanelVisible;
    },
    popInfoVisible() {
      return this.$store.state.popInfoVisible;
    },
    popVolumeVisible() {
      return this.$store.state.popVolumeVisible;
    },
    popShareVisible() {
      return this.$store.state.popShareVisible;
    },
    closedCaptionEnabled() {
      return this.$store.state.closedCaptionEnabled;
    },
    transcriptEnabled() {
      return this.$store.state.transcriptEnabled;
    },
    devicePaired() {
      return this.$store.state.devicePaired;
    },
    audioType() {
      return this.$store.state.audioType;
    },
    ctrlMenuAudioVisible() {
      return this.$store.state.ctrlMenuAudioVisible;
    },
    ctrlMenuVideoVisible() {
      return this.$store.state.ctrlMenuVideoVisible;
    },
  },
  mounted() {

    this.playAudioAnimation();
    this.playRecordingAnimation();
    this.checkCollision();

  },
  watch: {
    windowsSize(newVal) {
      this.checkCollision();
    },
  },
  methods: {
    playAudioAnimation() {
      const aduioMotion = mframe([{
        dom: document.getElementById('aduioIconRectBackground'),
        frames: [
            //2-17（15）
            { attr: {y:'8'} , time:0 },
            { attr: {y:'15.0'} , time:24, tween: 'easeInOut' },
            { attr: {y:'9.0'} , time:42, tween: 'easeInOut' },
            { attr: {y:'11.0'} , time:44, tween: 'easeInOut' },
            { attr: {y:'13.0'} , time:62, tween: 'easeInOut' },
            { attr: {y:'2.0'} , time:86, tween: 'easeInOut' },
            { attr: {y:'8.0'} , time:112, tween: 'easeInOut' },
            { attr: {y:'15.0'} , time:134, tween: 'easeInOut' },
            { attr: {y:'9.0'} , time:158, tween: 'easeInOut' },
            { attr: {y:'11.0'} , time:178, tween: 'easeInOut' },
            { attr: {y:'8.0'} , time:204, tween: 'easeInOut' }
        ]
      }]);
      aduioMotion.repeat(Infinity);
    },
    playRecordingAnimation() {
      if (this.me.host !== true) {
        return;
      }
      const recordingMotion = mframe([{
          dom: document.getElementById('recording-red-dot'),
          frames: [
              { attr: {fill:'#D93829', r: '4'} , time:0 },
              { attr: {fill:'#F7644A', r: '5.4'} , time:60, tween: 'easeInOut' },
              { attr: {fill:'#D93829', r: '4.0'} , time:120, tween: 'easeInOut' }
          ]
      }]);
      recordingMotion.repeat(Infinity);
    },
    togglePlist(evt) {
      if (!this.plistPanelVisible) {
        this.openPlist();
      } else {
        this.$store.commit('plistPanelVisible', false);
      }
    },
    toggleChat(evt) {
      if (!this.chatPanelVisible) {
        this.openChat();
      } else {
        this.$store.commit('chatPanelVisible', false);
      }
    },
    toggleCC(evt) {
      this.$store.commit('closedCaptionEnabled', !this.closedCaptionEnabled);
    },
    openPlist() {
      this.$store.commit("plistPanelVisible", true);
      this.$emit("openppanel", 'plist');
    },
    openChat() {
      this.$store.commit("chatPanelVisible", true);
      this.$emit("openppanel", 'chat');
    },
    openCC() {
      this.$store.commit("ccPanelVisible", true);
      this.$emit("openppanel", 'cc');
    },
    toggleVolume(evt) {
      if (!this.popVolumeVisible) {
        const appos = document.querySelector("#desktop").getBoundingClientRect();
        const offset = document.querySelector("#controls .btn_volume").getBoundingClientRect();
        const width = offset.width;
        const left = offset.left - appos.left + width / 2;
        const top = offset.top - appos.top;
        this.$store.commit("popStartPoint", [left, top]);
        this.$store.commit("popVolumeVisible", true);
      } else {
        this.$store.commit("popVolumeVisible", false);
      }
    },
    toggleShare(evt) {
      if (!this.popShareVisible) {
        const appos = document.querySelector("#desktop").getBoundingClientRect();
        const offset = document.querySelector("#controls .btn_share").getBoundingClientRect();
        const width = offset.width;
        const left = offset.left - appos.left + width / 2;
        const top = offset.top - appos.top;
        this.$store.commit("popStartPoint", [left, top]);
        this.$store.commit("popShareVisible", true);
      } else {
        this.$store.commit("popShareVisible", false);
      }
    },
    toggleRecord(evt) {
      
    },
    togglePopmenuMore(evt) {
      if (!this.popmenuMoreVisible) {
        const appos = document.querySelector("#desktop").getBoundingClientRect();
        const offset = document.querySelector("#controls .btn_more").getBoundingClientRect();
        const width = offset.width;
        const left = offset.left - appos.left + width / 2;
        const top = offset.top - appos.top;
        this.$store.commit("popStartPoint", [left, top]);
        this.$store.commit("popmenuMoreVisible", true);
      } else {
        this.$store.commit("popmenuMoreVisible", false);
      }
    },
    toggleMore(evt) {
      if (!this.popmenuMoreVisible) {
        const appos = document.querySelector("#desktop").getBoundingClientRect();
        const offset = document.querySelector(".btn_panels").getBoundingClientRect();
        const width = offset.width;
        const left = offset.left - appos.left + width / 2;
        const top = offset.top - appos.top;
        this.$store.commit("popStartPoint", [left, top]);
        this.$store.commit("popPanelVisible", true);
      } else {
        this.$store.commit("popPanelVisible", false);
      }
    },
    toggleAnnTools() {
      this.annoToolsVisible = !this.annoToolsVisible;
    },
    
    toggleMute(evt) {
      if (this.muted) {
        this.$store.commit('muted', false);

        let plist = [];
        const len = this.peopleList.length;
        for (let i = 0; i < len; i++) {
            const p = this.peopleList[i];
            if (p.me) {
                p.muted = false;
            }
            plist.push(p);
        }
        this.$store.commit("peopleList", plist);

      } else {
        this.$store.commit('muted', true);

        let plist = [];
        const len = this.peopleList.length;
        for (let i = 0; i < len; i++) {
            const p = this.peopleList[i];
            if (p.me) {
                p.muted = true;
            }
            plist.push(p);
        }
        this.$store.commit("peopleList", plist);

      }
    },
    toggleVideo(evt) {
      if (this.cameraOn) {
        this.$store.commit('cameraOn', false);

        let plist = [];
        const len = this.peopleList.length;
        for (let i = 0; i < len; i++) {
            const p = this.peopleList[i];
            if (p.me) {
                p.video = null;
            }
            plist.push(p);
        }
        this.$store.commit("peopleList", plist);

      } else {
        this.$store.commit('videoConnected', true);
        this.$store.commit('cameraOn', true);

        let plist = [];
        const len = this.peopleList.length;
        for (let i = 0; i < len; i++) {
            const p = this.peopleList[i];
            if (p.me) {
                p.video = 'camera';
            }
            plist.push(p);
        }
        this.$store.commit("peopleList", plist);
      }
    },

    showCCBubble(e) {
      if (!this.transcriptEnabled) {
        this.ccBubbleVisible = true;
      }
    },

    showAudioOptionMenu(e) {
      e.preventDefault();
      e.stopPropagation();

      const appos = document.querySelector("#desktop").getBoundingClientRect();
      const offset = document.querySelector("#controls .btn_audio2").getBoundingClientRect();
      const width = offset.width;
      const left = offset.left - appos.left + width / 2;
      const top = offset.top - appos.top;
      this.$store.commit("popStartPoint", [left, top]);
      this.$store.commit('ctrlMenuAudioVisible', true);
    },
    showVideoOptionMenu(e) {
      e.preventDefault();
      e.stopPropagation();

      const appos = document.querySelector("#desktop").getBoundingClientRect();
      const offset = document.querySelector("#controls .btn_video2").getBoundingClientRect();
      const width = offset.width;
      const left = offset.left - appos.left + width / 2;
      const top = offset.top - appos.top;
      this.$store.commit("popStartPoint", [left, top]);
      this.$store.commit('ctrlMenuVideoVisible', true);
    },
    showPopAudioOptions() {
      this.$store.commit("popAudioOptionsVisible", true);
    },
    
    checkCollision() {
      const wrapRect = this.$refs.controlwrap.getBoundingClientRect();
      const leaveRect = this.$refs.btnleave.getBoundingClientRect();

      if (wrapRect.right - leaveRect.right > 20 + 242 + 20) {
        this.showLabel = true;
      } else {
        this.showLabel = false;
      }
    },
  }
};
</script>

<style lang="scss">
#controls_wrap {
  position: absolute;
  width: 100%;
  height: 84px;
  user-select: none;

  #controls {
    height: 54px;
    border-radius: 27px;
    bottom: 14px;
    text-align: center;
    pointer-events: none;
    margin: 15px auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  #controls.bg {
    height: 56px;
    border-radius: 28px;
    padding: 10px;
    width: 216px;
  }
  #controls .btn .lft {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 52px;
    height: 52px;
    background-color: #FFF;
    border-radius: 26px;
    pointer-events: auto;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
