<template>
  <portal
    to="portal-plist"
    :disabled="!plistPanelPopouted"
    id="plist_panel"
    ref="panel"
    :class="[theme, {cls: plistPanelClsed}]"
  >
    <panel-header
      class="pheader"
      :title="title"
      :type="'plist'"
      :floating="plistPanelPopouted"
      @close="close"
      @collapse="collapse"
      @popout="popout"
    ></panel-header>
    <div class="scrollpane" v-show="!plistPanelClsed">
      <div class="search">
        <md-icon name="search_12"></md-icon>
        <span>Search</span>
      </div>
      <div class="list">
        <plist-item
          v-for="(p, index) in plistGroupByRoom"
          :key="index"
          :type="p.type"
          :name="p.name"
          :me="p.me"
          :host="p.host"
          :avatar="p.avatar"
          :video="p.video || p.type === 'room'"
          :audio="p.audio"
          :muted="p.muted"
          :room="p.room"
        ></plist-item>
      </div>
    </div>
  </portal>
</template>

<script>
export default {
  data() {
    return {
      
    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    title() {
      return 'Participants (' + this.peopleList.length + ')';
    },
    videoLayoutMode() {
      return this.$store.state.videoLayoutMode;
    },
    panelVisible() {
      return this.$store.state.panelVisible;
    },
    plistPanelVisible() {
      return this.$store.state.plistPanelVisible;
    },
    plistPanelClsed() {
      return this.$store.state.plistPanelClsed;
    },
    plistPanelPopouted() {
      return this.$store.state.plistPanelPopouted;
    },
    plistGroupByRoom() {
      return this.$store.state.plistGroupByRoom;
    },
    peopleList() {
      return this.$store.state.peopleList;
    },
    rightPanelWidth() {
      return this.$store.state.rightPanelWidth;
    },
    chatPanelVisible() {
      return this.$store.state.chatPanelVisible;
    },
    fullscreen() {
      return this.$store.state.fullscreen;
    },
    plistGroupByRoom() {
      let rooms = [];
      let plist = [];
      const len = this.peopleList.length;
      for (let i = 0; i < len; i++) {
        const p = this.peopleList[i];
        if (p.room) {
          if (rooms.indexOf(p.room) === -1) {
            rooms.push(p.room);
            plist.push({ type: "room", name: p.room });
            for (let j = 0; j < len; j++) {
              const p2 = this.peopleList[j];
              if (p2.room === p.room) {
                plist.push(p2);
              }
            }
          }
        } else {
          plist.push(p);
        }
      }
      return plist;
    }
  },
  mounted() {

  },
  watch: {
    plistPanelClsed(newVal) {
      if (newVal) {
        //if(this.$refs.panel) {
        //  his.$refs.panel.style.flex = "";
        //}
      } else {
        //if(this.$refs.panel) {
        //  this.$refs.panel.style.flex = "1";
        //}
      }
    }
  },
  methods: {
    close() {
      this.$store.commit("plistPanelVisible", false);
      setTimeout( _ => this.$store.commit("plistPanelPopouted", false), 100);
    },
    collapse() {
      this.$store.commit("plistPanelClsed", !this.plistPanelClsed);
    },
    popout() {
      if (!this.plistPanelPopouted) {
        this.$store.commit("plistPanelClsed", false);
      }
      this.$store.commit("plistPanelPopouted", !this.plistPanelPopouted);
    },
  }
};
</script>


