<template>
  <portal
    to="portal-polling"
    :disabled="!pollingPanelPopouted"
    id="polling_panel"
    ref="panel"
    :class="[theme, {cls: pollingPanelClsed}]"
  >
    <panel-header
      :title="title"
      :type="'polling'"
      :floating="pollingPanelPopouted"
      @close="close"
      @collapse="collapse"
      @popout="popout"
    ></panel-header>
    <div class="scrollpane" v-show="!pollingPanelClsed">
      <div class="list">
      </div>
    </div>
    <div class="bot" v-show="!pollingPanelClsed">

    </div>
  </portal>
</template>

<script>

export default {
  data() {
    return {
      title: "Polling",

    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    videoLayoutMode() {
      return this.$store.state.videoLayoutMode;
    },
    panelVisible() {
      return this.$store.state.panelVisible;
    },
    pollingPanelVisible() {
      return this.$store.state.pollingPanelVisible;
    },
    pollingPanelClsed() {
      return this.$store.state.pollingPanelClsed;
    },
    plistPanelClsed() {
      return this.$store.state.plistPanelClsed;
    },
    pollingPanelPopouted() {
      return this.$store.state.pollingPanelPopouted;
    },

    rightPanelWidth() {
      return this.$store.state.rightPanelWidth;
    },

    fullscreen() {
      return this.$store.state.fullscreen;
    }
  },
  mounted() {
  },
  watch: {},
  methods: {
    close() {
      this.$store.commit("pollingPanelVisible", false);
      setTimeout( _ => this.$store.commit("pollingPanelPopouted", false), 100);
    },
    collapse() {
      this.$store.commit("pollingPanelClsed", !this.pollingPanelClsed);
    },
    popout() {
      if (!this.pollingPanelPopouted) {
        this.$store.commit("pollingPanelClsed", false);
      }
      this.$store.commit("pollingPanelPopouted", !this.pollingPanelPopouted);
    },
  }
};
</script>


