      transform: menuDirection === 't' ? 'translate(-100%, -100%)' : 'translate(-100%, 0)',
      width: menuVideoName !== me.name ? '250px' : '310px',
    }"
    >

        <div class='wrap' v-if="menuVideoName !== me.name">
          <div class='grp' >
            <div class='itm'>
              <md-icon class="ico" name="chat_16" />
              <span class="lb" >Chat</span>
            </div>

            <div class='itm'>
              <svg class="ico" width="16" height="15" viewBox="0 0 16 15" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd" clip-rule="evenodd" d="M13.3173 9.71712L12.0006 11.0339L11.483 10.5171C11.327 10.3611 11.0734 10.3611 10.9174 10.5171C10.7614 10.6731 10.7614 10.9267 10.9174 11.0827L11.7174 11.8827C11.7958 11.9611 11.8982 12.0003 12.0006 12.0003C12.1022 12.0003 12.2046 11.9611 12.283 11.8827L13.8829 10.2827C14.0389 10.1267 14.0389 9.87312 13.8829 9.71712C13.7269 9.56112 13.4733 9.56112 13.3173 9.71712M12.4002 13.6C10.8562 13.6 9.60029 12.344 9.60029 10.8C9.60029 9.256 10.8562 8 12.4002 8C13.9441 8 15.2 9.256 15.2 10.8C15.2 12.344 13.9441 13.6 12.4002 13.6V13.6ZM0.825484 13.6L1.29986 11.7264C1.63105 10.4744 2.82859 9.6 4.21013 9.6H9.02031C8.88672 9.9776 8.80032 10.3776 8.80032 10.8C8.80032 11.9384 9.3387 12.9408 10.1627 13.6H0.825484ZM3.20058 4.8C3.20058 2.5944 4.9941 0.8 7.2004 0.8C9.4051 0.8 11.2002 2.5944 11.2002 4.8C11.2002 7.0056 9.4051 8.8 7.2004 8.8C4.9941 8.8 3.20058 7.0056 3.20058 4.8V4.8ZM12.4002 7.2C11.9842 7.2 11.5922 7.2848 11.2202 7.4144C11.7114 6.6616 12.0002 5.7648 12.0002 4.8C12.0002 2.1528 9.84668 0 7.2004 0C4.55252 0 2.40061 2.1528 2.40061 4.8C2.40061 6.4688 3.25657 7.94 4.55172 8.8H4.21013C2.46701 8.8 0.950279 9.9192 0.525498 11.5216L0.0263204 13.4008C-0.0360768 13.636 0.013521 13.8832 0.163914 14.0784C0.320707 14.2832 0.562296 14.4 0.825484 14.4H12.4002C14.3881 14.4 16 12.788 16 10.8C16 8.812 14.3881 7.2 12.4002 7.2V7.2Z" :fill="theme==='dark' ? 'white' : 'black'"/>
              </svg>
              <span class="lb" >Assign as host</span>
            </div>

