            </div>

          </div>

        </div>

        <div class='wrap' v-if="menuVideoName === me.name">
          <div class='grp' >

            <div class='itm' @click="toggleSelfInGrid" :class="{
              disabled: peopleList.length <= 2
            }">
              <md-icon class="ico" name="selfview_16" />
              <span class="lb" >{{!selfVideoWithOthers ? 'Show my video with others' : 'Show my video separately'}}</span>
            </div>

            <div class='itm' >
              <svg class="ico" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
