            </div>

          </div><!--video_layer-->
          <div id='hud'>
            <div class='icoset_left'>

            </div><!--icoset_left-->

            <div class='icoset_right'>
                <div v-show='!maximizeVideo && (stripIsOpen && !fullscreen || currentSharing === null) && peopleList.length > 1'
