                    <md-icon name="minimize_12" v-if="fullscreen" />
                </div>
                -->

            </div><!--icoset_right-->

          </div><!--hud-->
          
          <self-video v-show="cameraOn && !devicePaired && !selfVideoWithOthers2"></self-video>
