            <panel-plist v-if="plistPanelVisible" class="rpanel" ></panel-plist>
            <panel-chat v-if="chatPanelVisible" class="rpanel"></panel-chat>
            <panel-polling v-if="pollingPanelVisible" class="rpanel"></panel-polling>
            <panel-cc v-if="ccPanelVisible" class="rpanel"></panel-cc>
          </div>
        </div><!--side_column_panels-->

        <div class="spliter" v-if="rightPanelWidth > 0"></div>

      </div><!--con_right-->
