    <div id='con_left_bottom' :class="{fullscreen: fullscreen}">
      
    </div><!--con_left_bottom-->
    <controls
      @openppanel="openPanel"
      :size="fullscreen && currentSharing === null ? 'mini' : 'normal'"
      class="controls"
