    ></controls>

    <div id='fullscreenFloatingPanel' v-if="fullscreen && currentSharing != null" >
      <video-item
        class="bvideo"
        :isBgVideo='true'
        :floatingVideo='false'
        :name='activeSpeaker.name'
        :host='activeSpeaker.host'
        :avatar='activeSpeaker.avatar'
        :video='activeSpeaker.video'
        :audio='activeSpeaker.audio'
        :muted='activeSpeaker.muted'
      />
      <div class="label">
        <span class="lb">{{activeSpeaker.name}}</span>
        <md-icon class="ico" name="endpoint_12" />
      </div>
      <div class="bot">
        <controls size="mini" :showBackground="false" style="height: 72px;"></controls>
      </div>
    </div>

    <div class="floatingControls">
      <drag-resize :isResizable="false" :showShadow="false" :w="245" :h="56" :x="annoToolsX" :y="annoToolsY" class="anno-tools" v-if="annoToolsVisible">
        <img src="../assets/img/anno-tools.svg" />
      </drag-resize>
    </div>

  </div>
</template>

<script>
import mframe from 'mframe'
import { watchResize } from 'watch-resize'
import hitTest from '../utils/hitTest'
import {addClass, hasClass, removeClass, style } from 'dom-helpers';
import { Draggable } from "draggable-vue-directive";
import _ from 'lodash';

export default {
  name: 'MainClient',

  directives: {
    Draggable
  },

  data () {
    return {

      switchOpen: true,
      videoColumnWidth: null,
      videoRowHeight: null,
      viewSwitchBtnsRight: 0,
      panelIconPage: 1,
      panelMarginTop: '169px',
      aiBotExped: false,
      popmenuSwitchSharingVisible: false,
      popmenuZoomVisible: false,
      stripIsOpen: true,
      stripIsOpen2: true,
      stripIsOpen_b: true,
      stripIsOpen_b2: true,
      draggableValue: {},
      observer: null,
      annoToolsVisible: false,
      annoToolsX: 0,
      annoToolsY: 0,
      maximizeVideoTmp: null,
      maximizeVideoVisible: false,
      stripWidth: '',
      stripLeft: '0',
      showGradientMask: false,
    }
  },

  computed: {
    theme() {
      return this.$store.state.theme;
    },
    windowsSize(){
      return this.$store.state.windowsSize;
    },
    windowsWidth(){
      return this.$store.state.windowsWidth;
    },
    windowsHeight(){
      return this.$store.state.windowsHeight;
    },
    audioType() {
      return this.$store.state.audioType;
    },
    tipConnectingVisible() {
      return this.$store.state.tipConnectingVisible;
    },
    sidePanelIsOpen() {
      return this.$store.state.sidePanelIsOpen;
    },
    videoLayoutMode() {
      return this.$store.state.videoLayoutMode;
    },
    videoLayoutPrepareMode() {
      return this.$store.state.videoLayoutPrepareMode;
    },
    maximizeVideo() {
      return this.$store.state.maximizeVideo;
    },
    gridViewShowVideoOnly() {
      return this.$store.state.gridViewShowVideoOnly;
    },
    selfVideoWithOthers() {
      return this.$store.state.selfVideoWithOthers;
    },
    selfVideoWithOthers2() {
      const lst = this.peopleList.filter(
        (p, idx) => {
          return (!p.me || p.me && this.cameraOn) && (!this.gridViewShowVideoOnly || this.gridViewShowVideoOnly && p.video)}
      )
      const len = lst.length;

      if (this.videoLayoutMode === 'grid' && len === 2) {
        return true;
      }
      if (this.videoLayoutMode === 'strip' && this.peopleList.length <= 2) {
        return false;
      }
      if (this.videoLayoutMode === 'strip' && this.peopleList.length === 3) {
        return true;
      }
      return this.$store.state.selfVideoWithOthers;
    },
    activeSpeakerName() {
      return this.$store.state.activeSpeakerName;
    },
    activeSpeaker() {
      return this.$store.getters.activeSpeaker;
    },
    host() {
      return this.$store.getters.host;
    },
    me() {
      return this.$store.getters.me;
    },
    muted() {
      return this.$store.state.muted;
    },
    fullscreen() {
      return this.$store.state.fullscreen;
    },
    
    currentSharing() {
      return this.$store.state.currentSharing;
    },
    viewSharing() {
      return this.$store.state.viewSharing;
    },

    peopleList() {
      return this.$store.state.peopleList;
    },
    peopleNotJoinedList() {
      return this.$store.state.peopleNotJoinedList;
    },

    rightPanelContentWidth() {
      return this.$store.state.rightPanelContentWidth;
    },
    rightPanelWidth() {
      return this.$store.state.rightPanelWidth;
    },
    rightPanelOpenStack() {
      return this.$store.state.rightPanelOpenStack;
    },

    chatMsgList() {
      return this.$store.state.chatMsgList;
    },

    plistPanelVisible() {
      return this.$store.state.plistPanelVisible;
    },
    plistPanelClsed() {
      return this.$store.state.plistPanelClsed;
    },
    plistPanelPopouted() {
      return this.$store.state.plistPanelPopouted;
    },

    chatPanelVisible() {
      return this.$store.state.chatPanelVisible;
    },
    chatPanelClsed() {
      return this.$store.state.chatPanelClsed;
    },
    chatPanelPopouted() {
      return this.$store.state.chatPanelPopouted;
    },

    pollingPanelVisible() {
      return this.$store.state.pollingPanelVisible;
    },
    pollingPanelClsed() {
      return this.$store.state.pollingPanelClsed;
    },
    pollingPanelPopouted() {
      return this.$store.state.pollingPanelPopouted;
    },

    ccPanelVisible() {
      return this.$store.state.ccPanelVisible;
    },
    ccPanelClsed() {
      return this.$store.state.ccPanelClsed;
    },
    ccPanelPopouted() {
      return this.$store.state.ccPanelPopouted;
    },

    anyOpenPanel() {
      return this.$store.getters.anyOpenPanel;
    },
    anyPopoutPanel() {
      return this.$store.getters.anyPopoutPanel;
    },
    allPanelsPopouted() {
      return this.$store.getters.allPanelsPopouted;
    },
    cameraOn() {
      return this.$store.state.cameraOn;
    },
    closedCaptionEnabled() {
      return this.$store.state.closedCaptionEnabled;
    },
    closedCaptionAdded() {
      return this.$store.state.closedCaptionAdded;
    },
    popInfoVisible() {
      return this.$store.state.popInfoVisible;
    },
    devicePaired() {
      return this.$store.state.devicePaired;
    },
    devicePairedName() {
      return this.$store.state.devicePairedName;
    },
  },
  mounted () {

    window.addEventListener('keydown', this.onKeydown);
    this.setSwitch(this.videoLayoutMode);
    this.setViewMode(this.videoLayoutMode);

    this.observer = new MutationObserver(this.checkPanelStatus);
    this.observer.observe(this.$refs.side_column_panels, {
      attributes: true,
      characterData: true,
      childList: true,
      subtree: true,
      attributeOldValue: true,
      characterDataOldValue: true
    });
    this.checkPanelStatus();
  },

  watch: {

    windowsSize(newVal) {
      this.$nextTick(() => {
        if (this.videoLayoutMode === 'grid') {
          this.calcGridVideoSize();
        } else if (this.videoLayoutMode === 'strip') {
          this.calcStripSize();
        }
      });
    },

    videoLayoutMode(newVal) {
      this.setViewMode(newVal);

      if (this.rightPanelWidth > 0 && !this.sidePanelIsOpen && newVal !== 'side-by-side') {
        this.$store.commit('rightPanelWidth', 0);
      }
    },

    cameraOn(newVal) {
      this.$nextTick(() => {
        if (this.videoLayoutMode === 'grid') {
          this.calcGridVideoSize();
        } else if (this.videoLayoutMode === 'strip') {
          this.calcStripSize();
        }
      });
    },

    peopleList(newVal, oldVal) {
      if (this.videoLayoutMode === 'grid') {
        this.calcGridVideoSize();
      } else if (this.videoLayoutMode === 'strip') {
        this.calcStripSize();
      }
    },

    gridViewShowVideoOnly(newVal) {
      this.calcGridVideoSize();
    },
    selfVideoWithOthers(newVal) {
      if (this.videoLayoutMode === 'grid') {
        this.calcGridVideoSize();
      } else if (this.videoLayoutMode === 'strip') {
        this.calcStripSize();
      }
    },
    currentSharing(newVal) {
      if (this.videoLayoutMode === 'grid') {
        this.calcGridVideoSize();
      } else if (this.videoLayoutMode === 'strip') {
        this.calcStripSize();
      }
    },

    maximizeVideo(newVal) {
      if (newVal) {
        this.maximizeVideoTmp = newVal;
        this.maximizeVideoVisible = true;
        this.$nextTick(() => {
          const item = document.querySelector(`#video-item_${this.maximizeVideoTmp.name.replace(/ /g, '-')} .video`); 
          const maxitem = document.getElementById('maximize_video');
          const parentRect = document.getElementById('video_layer').getBoundingClientRect();
          const itemRect = item.getBoundingClientRect();

          const style1 = {
            left: `${itemRect.left - parentRect.left}px`,
            top: `${itemRect.top - parentRect.top}px`,
            width: `${itemRect.width}px`,
            height: `${itemRect.height}px`,
          };

          const style2 = {
            left: '0px',
            top: '0px',
            width: `${parentRect.width}px`,
            height: `${parentRect.height}px`,
          };

          const trans = mframe([{
              dom: maxitem,
              frames: [
                  { css: style1, time:0 },
                  { css: style2, time:20, tween: 'easeInOut' }
              ]
          }]);
          trans.play();

        });
        
      } else {

        const item = document.querySelector(`#video-item_${this.maximizeVideoTmp.name.replace(/ /g, '-')} .video`); 
        const maxitem = document.getElementById('maximize_video');
        const parentRect = document.getElementById('video_layer').getBoundingClientRect();
        const itemRect = item.getBoundingClientRect();

        const style1 = {
          left: `${itemRect.left - parentRect.left}px`,
          top: `${itemRect.top - parentRect.top}px`,
          width: `${itemRect.width}px`,
          height: `${itemRect.height}px`,
        };

        const style2 = {
          left: '0px',
          top: '0px',
          width: `${parentRect.width}px`,
          height: `${parentRect.height}px`,
        };

        const trans2 = mframe([{
            dom: maxitem,
            frames: [
                { css: style2, time:0 },
                { css: style1, time:20, tween: 'easeInOut' }
            ]
        }], {
          end: () => {
              this.maximizeVideoVisible = false;
            }
        });
        trans2.play();
        

      }
    },

    plistPanelVisible(newVal) {
      if (!newVal && (!this.anyOpenPanel && !this.anyPopoutPanel)) {
        this.closeSidePanel();
      }

      let arr = this.rightPanelOpenStack;
      if (newVal) {
        arr.push('plist');
        this.$store.commit('rightPanelOpenStack', arr);
      } else {
        _.remove(arr, function(n) {
          return n == 'plist';
        });
        this.$store.commit('rightPanelOpenStack', arr);
      }
    },

    chatPanelVisible(newVal) {
      if (!newVal && (!this.anyOpenPanel && !this.anyPopoutPanel)) {
        this.closeSidePanel();
      }

      let arr = this.rightPanelOpenStack;
      if (newVal) {
        arr.push('chat');
        this.$store.commit('rightPanelOpenStack', arr);
      } else {
        _.remove(arr, function(n) {
          return n == 'chat';
        });
        this.$store.commit('rightPanelOpenStack', arr);
      }
    },

    pollingPanelVisible(newVal) {
      if (!newVal && (!this.anyOpenPanel && !this.anyPopoutPanel)) {
        this.closeSidePanel();
      }else if(!this.pollingPanelPopouted){
        this.openSidePanel()
      }

      let arr = this.rightPanelOpenStack;
      if (newVal) {
        arr.push('polling');
        this.$store.commit('rightPanelOpenStack', arr);
      } else {
        _.remove(arr, function(n) {
          return n == 'polling';
        });
        this.$store.commit('rightPanelOpenStack', arr);
      }
    },

    ccPanelVisible(newVal) {
      if (!newVal && (!this.anyOpenPanel && !this.anyPopoutPanel)) {
        this.closeSidePanel();
      }else if(!this.pollingPanelPopouted){
        this.openSidePanel()
      }

      let arr = this.rightPanelOpenStack;
      if (newVal) {
        arr.push('cc');
        this.$store.commit('rightPanelOpenStack', arr);
      } else {
        _.remove(arr, function(n) {
          return n == 'cc';
        });
        this.$store.commit('rightPanelOpenStack', arr);
      }
    },



    plistPanelPopouted(newVal) {
      if (newVal && this.allPanelsPopouted) {
        this.closeSidePanel();
      } else if (this.plistPanelVisible) {
        this.openSidePanel();
      }

      let arr = this.rightPanelOpenStack;
      if (!newVal) {
        arr.push('plist');
        this.$store.commit('rightPanelOpenStack', arr);
      } else {
        _.remove(arr, function(n) {
          return n == 'plist';
        });
        this.$store.commit('rightPanelOpenStack', arr);
      }
    },
    
    chatPanelPopouted(newVal) {
      if (newVal && this.allPanelsPopouted) {
        this.closeSidePanel();
      } else if (this.chatPanelVisible) {
        this.openSidePanel();
      }

      let arr = this.rightPanelOpenStack;
      if (!newVal) {
        arr.push('chat');
        this.$store.commit('rightPanelOpenStack', arr);
      } else {
        _.remove(arr, function(n) {
          return n == 'chat';
        });
        this.$store.commit('rightPanelOpenStack', arr);
      }
    },    

    pollingPanelPopouted(newVal) {
      if (newVal && this.allPanelsPopouted) {
        this.closeSidePanel();
      } else if (this.pollingPanelVisible) {
        this.openSidePanel();
      }

      let arr = this.rightPanelOpenStack;
      if (!newVal) {
        arr.push('polling');
        this.$store.commit('rightPanelOpenStack', arr);
      } else {
        _.remove(arr, function(n) {
          return n == 'polling';
        });
        this.$store.commit('rightPanelOpenStack', arr);
      }
    },

    ccPanelPopouted(newVal) {
      if (newVal && this.allPanelsPopouted) {
        this.closeSidePanel();
      } else if (this.pollingPanelVisible) {
        this.openSidePanel();
      }

      let arr = this.rightPanelOpenStack;
      if (!newVal) {
        arr.push('cc');
        this.$store.commit('rightPanelOpenStack', arr);
      } else {
        _.remove(arr, function(n) {
          return n == 'cc';
        });
        this.$store.commit('rightPanelOpenStack', arr);
      }
    },



    plistPanelClsed(newVal) {
      if (!newVal) {
        this.adjustClsedPanel('plist');
      }
    },
    
    chatPanelClsed(newVal) {
      if (!newVal) {
        this.adjustClsedPanel('chat');
      }
    },    

    pollingPanelClsed(newVal) {
      if (!newVal) {
        this.adjustClsedPanel('polling');
      }
    },

    ccPanelClsed(newVal) {
      if (!newVal) {
        this.adjustClsedPanel('cc');
      }
    },
   

  },
  methods: {
    toggleInfo() {
      this.$store.commit("popInfoVisible", !this.popInfoVisible);
    },

    checkPanelStatus() {
      const len = this.rightPanelOpenStack.length;
      let clsed = 0;
      if (len > 2) {
        for (var i=0; i<len; i++) {
          const pname = this.rightPanelOpenStack[i];
          if (this[pname+'PanelClsed']) {
            clsed++;
          }
        }
        if (clsed === 0) {
          const p = this.rightPanelOpenStack[0];
          this.$store.commit(p+'PanelClsed', true);
        }
      }
    },
    adjustClsedPanel(panelName) {
      const len = this.rightPanelOpenStack.length;
      let clsed = 0;
      if (len > 2) {
        for (var i=0; i<len; i++) {
          const pname = this.rightPanelOpenStack[i];
          if (this[pname+'PanelClsed']) {
            clsed++;
          }
        }
        if (clsed === 0) {
          for (var i=0; i<len; i++) {
            const pname = this.rightPanelOpenStack[i];
            if (pname !== panelName) {
              this.$store.commit(pname+'PanelClsed', true);
              break;
            }
          }
        }
      }
    },

    openSwitch(evt) {
      this.switchOpen = true;
      this.viewSwitchBtnsRight = 0;
      window.addEventListener('mousemove', this.checkMouseLeaveSwitch);

    },

    closeSwitch(mode) {
      //this.switchOpen = false;
      this.setSwitch(mode);
      window.removeEventListener('mousemove', this.checkMouseLeaveSwitch);

    },

    checkMouseLeaveSwitch(evt) {
      if (!hitTest(this.$refs.btn_view_switch, evt.pageX, evt.pageY)) {
        this.closeSwitch();
      }
    },

    setSwitch(mode) {
      if (this.switchOpen) {
        return;
      }
      if (!mode) {
        mode = this.videoLayoutMode;
      }
      let btn = this.$el.querySelector(`.btn-${mode}`);
      if (btn) {
        let right = this.currentSharing === null ? btn.offsetLeft - 64 : btn.offsetLeft - 32;
        this.viewSwitchBtnsRight = right + 'px';
      }
    },

    toggleSidePanel() {
      if (!this.sidePanelIsOpen) {
        this.openSidePanel();
      } else {
        this.closeSidePanel();
      }
    },

    prepareSidePanelChange() {
      this.watchResize();

      if (!this.sidePanelIsOpen) {
        const trans = mframe([{
            dom: this.$refs.side_column_panels,
            frames: [
                { css: {left: '100%'} , time:0 },
                { css: {left: 0} , time:10, tween: 'easeInOut' }
            ]
        }]);
        trans.play();
      }

      this.$refs.con_right.style.transition = 'width 0.2s ease-out';
      setTimeout( () => {
        this.$refs.con_right.style.transition = '';
      }, 500);
    },

    openPanel(type) {
      if(type === 'plist' && !this.plistPanelPopouted){
        this.openSidePanel()
      } else if(type === 'chat' && !this.chatPanelPopouted){
        this.openSidePanel()
      } else if(type === 'trans' && !this.transPanelPopouted){
        this.openSidePanel()
      } else if(type === 'cc' && !this.ccPanelPopouted){
        this.openSidePanel()
      } else if(type === 'polling' && !this.pollingPanelPopouted){
        this.openSidePanel()
      }
    },

    openSidePanel() {
      this.prepareSidePanelChange();

      this.$store.commit('sidePanelIsOpen', true);
      if (this.rightPanelWidth === 0) {
        this.$refs.side_column_panels.style.left = '0';
        this.$store.commit('rightPanelWidth', this.rightPanelContentWidth);
      }

      if (!this.anyOpenPanel) {
        this.$store.commit('plistPanelVisible', true);
      }
    },

    closeSidePanel() {
      if (!this.anyOpenPanel) {
        this.panelIconPage = 1;
      }
      this.prepareSidePanelChange();

      this.$store.commit('sidePanelIsOpen', false);
      if (this.videoLayoutMode === 'side-by-side') {

          const trans = mframe([{
              dom: this.$refs.side_column_panels,
              frames: [
                  { css: {left: 0} , time:0 },
                  { css: {left: '100%'} , time:10, tween: 'easeInOut' }
              ]
          }]);
          trans.play();

      } else {
        this.$store.commit('rightPanelWidth', 0);
      }
    },

    switchTo(mode) {
      if (this.videoLayoutMode != mode) {
        this.$store.commit('videoLayoutMode', mode);
      }
      if (this.currentSharing !== null) {
        if (this.videoLayoutMode === 'side-by-side') {
          this.$store.commit('rightPanelWidth', this.rightPanelContentWidth);
          if (!this.sidePanelIsOpen) {
            this.$refs.side_column_panels.style.left = '100%';
          } else {
            this.$refs.side_column_panels.style.left = '0';
          }
        } else if (this.videoLayoutMode === 'strip') {
          if (!this.sidePanelIsOpen) {
            this.$store.commit('rightPanelWidth', 0);
          }
        }
      }
    },

    setViewMode(mode) {
      if (mode === 'strip') {
        this.$nextTick(() => {
          this.calcStripSize();
        });
        
      } else if (mode === 'grid') {
        this.$nextTick(() => {
          this.calcGridVideoSize();
        });
        
      }
    },

    watchResize() {
      // watch resize for video scrollpane
      const target = this.$refs.video_scrollpane;
      if (target) {
        watchResize(target).then(([resize, destroyResize]) => {
          resize.subscribe(({ element, event, prevBoundingClientRect }) => {
            this.calcGridVideoSize();
          });
          setTimeout(() => {
            destroyResize();
          }, 1500);
        });
      }
    },

    calcStripSize() {
      const parentRect = document.getElementById('video_layer').getBoundingClientRect();
      this.showGradientMask = false;

      const per = !this.selfVideoWithOthers2 ? '68%' : '94%';

      const lst = this.peopleList.filter(
        (p, idx) => {
          return (!p.me || p.me && this.selfVideoWithOthers2 && this.cameraOn)
        }
      )
      const len = lst.length;

      if (!this.currentSharing && len > 1) {
        if (this.$refs.video_scrollpane) {
          this.$refs.video_scrollpane.style.top = 'auto';
          if (this.stripIsOpen_b) {
            this.$refs.video_scrollpane.style.bottom = '10px';
          } else {
            this.$refs.video_scrollpane.style.bottom = '-95px';
          }
        }
        
        const num = len - 1;
        const w = num * (160 + 10);
        this.stripWidth = `min(${w}px, ${per})`;
        this.stripLeft = `calc((100% - min(${w}px, ${per})) / 2)`;

        if (w > parentRect.width * 0.68) {
          this.showGradientMask = true;
        }
      } else if (this.currentSharing) {
        if (this.$refs.video_scrollpane) {
          this.$refs.video_scrollpane.style.bottom = 'auto';
          if (this.stripIsOpen) {
            this.$refs.video_scrollpane.style.top = '12px';
          } else {
            this.$refs.video_scrollpane.style.top = '-91px';
          }
        }
        
        const num = len;
        const w = num * (160 + 10);
        this.stripWidth = `min(${w}px, ${per})`;
        this.stripLeft = `calc((100% - min(${w}px, ${per})) / 2)`;

        if (w > parentRect.width * 0.68) {
          this.showGradientMask = true;
        }
      }

    },

    getGridPeopleNum() {
      const lst = this.peopleList.filter(
        (p, idx) => {
          return (!p.me || p.me && this.selfVideoWithOthers2) && (!this.gridViewShowVideoOnly || this.gridViewShowVideoOnly && p.video)}
      )
      const len = lst.length;
      return len;
    },

    calcGridVideoSize() {
      this.$refs.video_scrollpane.style.bottom = '0px';

      const rect = this.$refs.video_scrollpane.getBoundingClientRect();
      const switchRect = this.$refs.btn_view_switch.getBoundingClientRect();

      const len = this.getGridPeopleNum();
      const gap = 4;

      if (len === 1) {
        this.videoColumnWidth = `calc(100% - 20px)`;
        this.videoRowHeight = `calc(100% - 4px)`;
      } else if (len === 2) {
        let w = (rect.width - 20 - gap)/2;
        let h = w / 16 * 9;
        if ((h+gap) * 2 > rect.height) {
          h = (rect.height - gap) /2;
          w = h / 9 * 16;
        }
        const hpx = h + 'px';
        this.videoColumnWidth = `calc((100% - 22px)/2)`;
        this.videoRowHeight = `${hpx}`;
      } else if (len <= 4) {

        let w = (rect.width - 20 - gap)/2;
        let h = w / 16 * 9;
        if (h * 2 + gap > rect.height) {
          h = (rect.height - gap) /2;
          w = h / 9 * 16;
        }

        // avoid overlay switch button
        if (
          rect.left + rect.width/2 + gap/2 + w > switchRect.left - 5
          && rect.top + rect.height/2 - gap/2 - h < switchRect.bottom + 5
        ) {
            w = switchRect.left - rect.left - rect.width / 2 - gap - 10;
            h = w / 16 * 9;
        }

        const wpx = w + 'px';
        const hpx = h + 'px';
        this.videoColumnWidth = `${wpx}`;
        this.videoRowHeight = `${hpx}`;
        
      } else if (len <= 6) {
        const w = (rect.width - 20 - gap * 2)/3;
        const wpx = w + 'px';
        const h = w / 16 * 9;
        const hpx = h + 'px';
        this.videoColumnWidth = `calc((100% - 20px - 8px)/3)`;
        this.videoRowHeight = `${hpx}`;
      } else if (len <= 8) {
        const w = (rect.width - gap * 3) * 0.25;
        const wpx = w + 'px';
        const h = w / 16 * 9;
        const hpx = h + 'px';
        this.videoColumnWidth = `${wpx}`;
        this.videoRowHeight = `${hpx}`;
      } else if (len <= 12) {

        let w = (rect.width - gap * 3) * 0.25;
        let h = w / 16 * 9;
        if (h * 3 + gap * 2 > rect.height) {
          h = (rect.height - gap * 2) / 3;
          w = h / 9 * 16;
        }

        // avoid overlay switch button
        

        const wpx = w + 'px';
        const hpx = h + 'px';
        this.videoColumnWidth = `${wpx}`;
        this.videoRowHeight = `${hpx}`;
      }
      
    },

    onKeydown(evt) {
      // console.log('evt.code', evt.code, evt.altKey);

      if (evt.code == 'KeyS' && evt.altKey) {

        // Alt + S
        if (this.currentSharing === null) {
          if (this.$refs.video_scrollpane) {
            this.$refs.video_scrollpane.style.bottom = 'auto';
            if (this.stripIsOpen) {
              this.$refs.video_scrollpane.style.top = '12px';
            } else {
              this.$refs.video_scrollpane.style.top = '-91px';
            }
          }

          // start sharing
          this.$store.commit('currentSharing', 'dsk');

          this.$nextTick(() => {
            if (this.videoLayoutMode === 'grid') {
              this.switchTo('side-by-side');
            } else {
              this.switchTo('strip');
            }
          });

        } else {
          if (this.$refs.video_scrollpane) {
            this.$refs.video_scrollpane.style.top = 'auto';
            if (this.stripIsOpen_b) {
              this.$refs.video_scrollpane.style.bottom = '10px';
            } else {
              this.$refs.video_scrollpane.style.bottom = '-91px';
            }
          }
          
          if (this.videoLayoutMode === 'side-by-side') {
            this.switchTo('grid');
          }

          this.stopSharing();
        }

      } else if (evt.code == 'KeyA' && evt.altKey) {
        if (this.peopleNotJoinedList.length > 0) {
          let p1 = this.peopleList.slice();
          let p2 = this.peopleNotJoinedList.slice();
          const p = p2.shift();
          p1.push(p);
          this.$store.commit('peopleNotJoinedList',p2);
          this.$store.commit('peopleList', p1);
        }
        
      } else if (evt.code == 'KeyR' && evt.altKey) {
        if (this.peopleList.length > 1) {
          let p1 = this.peopleList.slice();
          let p2 = this.peopleNotJoinedList.slice();
          const p = p1.pop();
          p2.unshift(p);
          this.$store.commit('peopleNotJoinedList', p2);
          this.$store.commit('peopleList', p1);
        }
        
      }


    },

    showPanelIconPage(page) {
      this.panelIconPage = page;
    },

    toggleAiBot() {
      this.aiBotExped = !this.aiBotExped;
    },

    togglePopmenuSwitchSharing() {
      this.popmenuSwitchSharingVisible = !this.popmenuSwitchSharingVisible;
      this.popmenuZoomVisible = false;
    },
    togglePopmenuZoom() {
      this.popmenuZoomVisible = !this.popmenuZoomVisible;
      this.popmenuSwitchSharingVisible = false;
    },

    toggleStrip_b() {
      if (this.stripIsOpen_b) {
        this.stripIsOpen_b2 = false;

        const trans = mframe([{
            dom: this.$refs.video_scrollpane,
            frames: [
                { css: {bottom: '10px'} , time:0 },
                { css: {bottom: '-95px'} , time:20, tween: 'easeInOut' }
            ]
          }], {
            end: () => {
              this.stripIsOpen_b = false
            }
        });
        trans.play();

      } else {
        this.stripIsOpen_b = true;
        this.stripIsOpen_b2 = true;

        const trans = mframe([{
            dom: this.$refs.video_scrollpane,
            frames: [
                { css: {bottom: '-95px'} , time:0 },
                { css: {bottom: '10px'} , time:20, tween: 'easeInOut' }
            ]
        }]);
        trans.play();
      }
    },

    toggleStrip() {
      if (this.stripIsOpen) {
        this.stripIsOpen2 = false;

        const trans = mframe([{
            dom: this.$refs.video_scrollpane,
            frames: [
                { css: {top: '12px'} , time:0 },
                { css: {top: '-91px'} , time:20, tween: 'easeInOut' }
            ]
          }], 
          {
            end: () => {
              this.stripIsOpen = false
            }
        });
        trans.play();

      } else {
        this.stripIsOpen = true;
        this.stripIsOpen2 = true;

        const trans = mframe([{
            dom: this.$refs.video_scrollpane,
            frames: [
                { css: {top: '-91px'} , time:0 },
                { css: {top: '12px'} , time:20, tween: 'easeInOut' }
            ]
        }]);
        trans.play();

      }
    },

    toggleAnnTools() {
      this.annoToolsVisible = !this.annoToolsVisible;

      if (this.annoToolsVisible ) {
        this.$nextTick(() => {
          this.setAnnoToolsPosition();
        });
      }
    },

    setAnnoToolsPosition() {
      const btn = document.querySelector('.btn-anno');
      const tool = document.querySelector('.anno-tools');
      const rect = btn.getBoundingClientRect();

      const root = document.querySelector('#mainclient');
      const rectRoot = root.getBoundingClientRect();

      this.annoToolsX = rect.left - rectRoot.left - 80;
      this.annoToolsY = rect.top + 32;
    },

    stopSharing() {
      // stop sharing
      this.$store.commit('fullscreen', false);
      this.$store.commit('currentSharing', null);
      this.annoToolsVisible = false;
    },

    toggleFullscreen() {
      this.$store.commit("fullscreen", !this.fullscreen);


      if (this.videoLayoutMode === 'grid') {
        let cnt = 0;
        let itv = setInterval(() => {
          this.calcGridVideoSize();
          cnt++;
          if (cnt > 30) {
            clearInterval(itv);
          }
        }, 10);
      }

      if (this.annoToolsVisible ) {
        let cnt = 0;
        let itv = setInterval(() => {
          this.setAnnoToolsPosition();
          cnt++;
          if (cnt > 50) {
            clearInterval(itv);
          }
        }, 10);

      }

    },

  },

}
</script>

