<template>
  <portal
    to="portal-cc"
    :disabled="!ccPanelPopouted"
    id="cc_panel"
    ref="panel"
    :class="[theme, {cls: ccPanelClsed, minheight: openedPanelCnt <=2 && !ccPanelClsed && !chatPanelVisible}]"
    
  >
    <panel-header
      :title="title"
      :type="'cc'"
      :floating="ccPanelPopouted"
      @close="close"
      @collapse="collapse"
      @popout="popout"
    ></panel-header>

    
    <div class="tabs" v-show="!ccPanelClsed">
    </div>
    <div class="scrollpane" v-show="!ccPanelClsed">
      <div class="placeholder" v-if="!closedCaptionAdded"></div>
      <div class="list" v-show="closedCaptionAdded"></div>
    </div>

  </portal>
</template>

<script>

export default {
  data() {
    return {
      title: "Captions and Highlights",

    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    videoLayoutMode() {
      return this.$store.state.videoLayoutMode;
    },
    panelVisible() {
      return this.$store.state.panelVisible;
    },
    chatPanelVisible() {
      return this.$store.state.chatPanelVisible;
    },
    ccPanelVisible() {
      return this.$store.state.ccPanelVisible;
    },
    ccPanelClsed() {
      return this.$store.state.ccPanelClsed;
    },
    ccPanelPopouted() {
      return this.$store.state.ccPanelPopouted;
    },

    closedCaptionAdded() {
      return this.$store.state.closedCaptionAdded;
    },

    rightPanelWidth() {
      return this.$store.state.rightPanelWidth;
    },

    fullscreen() {
      return this.$store.state.fullscreen;
    },
    openedPanelCnt() {
      return this.$store.getters.openedPanelCnt;
    },
  },
  mounted() {
  },
  watch: {},
  methods: {
    close() {
      this.$store.commit("ccPanelVisible", false);
      setTimeout( _ => this.$store.commit("ccPanelPopouted", false), 100);
    },
    collapse() {
      this.$store.commit("ccPanelClsed", !this.ccPanelClsed);
    },
    popout() {
      if (!this.ccPanelPopouted) {
        this.$store.commit("ccPanelClsed", false);
      }
      this.$store.commit("ccPanelPopouted", !this.ccPanelPopouted);
    },
  }
};
</script>


