<template>
    <div
    id="popmenu_more"
    ref="popover"
    v-if="popmenuMoreVisible"
    :class="[theme]"
    :style="{
      left: popStartPoint[0] + 'px',
      top: (popStartPoint[1] - 18) + 'px',
    }"
    >
      <div class='triangle'></div>
        <div class='wrap'>

          <div class='grp' @click="!devicePaired ? showPopDevice() : showPopAudioOptions()">
            <div class='tt'>
              <span class="lb">{{!devicePaired ? 'Video system' : 'Audio connection'}}</span>
            </div>
            <div class='itm' v-if="!devicePaired">
              <md-icon name="device-connection_16" />
              <span class="lb" >Connect to a video system</span>
            </div>
            <div class='itm' v-if="devicePaired">
              <md-icon name="handset_16" />
              <span class="lb" >Switch to audio</span>
            </div>
          </div>

          <div class='grp' >
            <div class='tt'>
              <span class="lb">More options</span>
            </div>
            <div class='itm'>
              <md-icon name="private-circle_16" />
              <span class="lb" >Lock Meeting</span>
              <md-toggle-switch label="" htmlId="lock-meeting" class="toggle-switch-m" @change="toggleLock" :checked="meetingLocked"></md-toggle-switch>
            </div>

            <div class='itm'>
              <md-icon name="participant-add_16" />
              <span class="lb" >Invite and Remind</span>
            </div>

            <div class='itm' @click="copy">
              <md-icon name="link_16" />
              <span class="lb" >Copy Meeting link</span>
              <md-badge class="copied" color="green" v-if="copiedVisible">Copied</md-badge>
            </div>

            <div class='itm cl'>
              <div class='rw' @click="showStreaming">
                <md-icon name="streaming_16" />
                <span class="lb" >{{streamingIsOn ? 'Stop Streaming' : 'Streaming'}}</span>
              </div>
            </div>

          </div>

        </div>
    </div>
</template>

<script>
import hitTest from "../utils/hitTest";

export default {
  data() {
    return {
      settingMenuType: '',
      copiedVisible: false,
    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },
    audioConnected() {
      return this.$store.state.audioConnected;
    },
    audioType() {
      return this.$store.state.audioType;
    },
    popmenuMoreVisible() {
      return this.$store.state.popmenuMoreVisible;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },
    meetingLocked() {
      return this.$store.state.meetingLocked;
    },
    devicePaired() {
      return this.$store.state.devicePaired;
    },
    streamingIsOn() {
      return this.$store.state.streamingIsOn;
    },

  },
  mounted() {},
  watch: {
    popmenuMoreVisible(newVal) {
      if (newVal) {
        window.addEventListener("mousedown", this.onMouseDown);
      } else {
        window.removeEventListener("mousedown", this.onMouseDown);
      }
    }
  },
  methods: {
    onMouseDown(evt) {
      if (
        this.$refs.popover &&
        !hitTest(
          document.querySelector("#controls .btn_more"),
          evt.pageX,
          evt.pageY
        ) &&
        !hitTest(this.$refs.popover, evt.pageX, evt.pageY)
      ) {
        this.close();
      }

      
    },
    toggleLock(evt) {
      this.$store.commit("meetingLocked", evt.target.checked);
    },
    copy() {
      this.copiedVisible = true;
      setTimeout( _ => {
        this.copiedVisible = false;
      }, 3000);
    },
    showStreaming() {
      this.close();
      this.$store.commit('popStreamingVisible', true);
    },
    showPopAudioOptions() {
      this.close();
      this.$store.commit("popAudioOptionsVisible", true);
    },
    showPopDevice(evt) {
      this.close();
      this.$store.commit('popDevicePairingVisible', true);
    },

    close() {
      this.$store.commit("popmenuMoreVisible", false);
    },

  }
};
</script>

