<template>
    <div class='video_item' :class="[theme]" v-show='videoVisible' :data-name='name' >
        <div 
            class='video'
            @mouseenter="mouseover" 
            @mouseleave="mouseout" 
        >
            <div class="bg"></div>
            <video
                ref="video"
                :src='videoUrl'
                autoplay='true'
                loop='true'
                @dblclick="toggleMaximize"
                v-show="video !== null"
            ></video>

            <div class='avatar'
                v-if='video === null && avatar || me.name === name && !cameraOn'
                :style='{backgroundImage:avatarUrl}'
                :class='{
                    grid: videoLayoutMode=="grid",
                    size200: peopleList.length === 1,
                }'
            ></div>
            <div class="bigname" v-if="video === null && !avatar" :class="{abbr: videoLayoutMode === 'strip'}">
                <div 
                    class="lb" 
                    :class="{
                        small: videoLayoutMode === 'side-by-side',
                    }"
                >
                    {{ videoLayoutMode === 'grid' || videoLayoutMode === 'side-by-side' ? name : abbr }}
                </div>
                <div class="ico" v-if="videoLayoutMode==='grid' || videoLayoutMode === 'side-by-side'">
                    <span class="md-icon icon" :class="audioIco" ></span>
                    <md-icon name="microphone-muted_12" v-if="muted2" color="red-60" />
                </div>
            </div>
            <div class='lb_name'
                v-if=" (video !== null || avatar !== null || video === null && videoLayoutMode !== 'grid' && videoLayoutMode !== 'side-by-side') && !isBgVideo && peopleList.length > 1 "
                :class='{
                    lb_small:(videoLayoutMode=="strip" || videoLayoutMode=="side-by-side" && !isBgVideo) && !isMaxVideo,
                    lb_big:videoLayoutMode=="grid" || isMaxVideo,
                    lb_muted:videoLayoutMode=="grid" && muted2,
                    lb_muted2:(videoLayoutMode=="side-by-side" && sideBySideVideoColumn == 1) && muted2
                }' >
                <div class="ico"><span class="md-icon icon" :class="audioIco" ></span></div>
                <div class='txt' >{{ name }}</div>
                <div class="ico"><md-icon name="microphone-muted_12" v-if="muted2" color="red-60" /></div>
            </div>

            <div 
                class="ctrl" 
                ref="ctrl"
                v-if="ctrlVisible"
                :class="{
                    center: (videoLayoutMode==='strip' || videoLayoutMode==='side-by-side') && !isBgVideo && !isMaxVideo,
                    sidebyside1: sidebysideTop,
                }"
            >
                <div 
                    v-if="peopleList.length > 1"
                    class="btn btn-mute" 
                    :class="{off: controlOffRedStyle === 1 && muted}"
                    @click="!muted2 ? mute($event) : unmute($event)" 
                >
                    <svg v-if="!muted2" class="ico" width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" clip-rule="evenodd" d="M4.5 7.5V2.5C4.5 1.673 5.173 1 6 1C6.827 1 7.5 1.673 7.5 2.5V7.5C7.5 8.327 6.827 9 6 9C5.173 9 4.5 8.327 4.5 7.5M8.5 7.5V2.5C8.5 1.125 7.375 0 6 0C4.625 0 3.5 1.125 3.5 2.5V7.5C3.5 8.704 4.362 9.716 5.5 9.949V11H5.501H4.5C4.225 11 4 11.226 4 11.5C4 11.775 4.225 12 4.5 12H7.5C7.775 12 8 11.775 8 11.5C8 11.226 7.775 11 7.5 11H6.5V9.949C7.638 9.716 8.5 8.704 8.5 7.5" fill="#44CF50"/>
                        <rect x="4" y="6" width="4" height="3" fill="#44CF50"/>
                        <rect x="4" y="4" width="4" height="1" fill="#44CF50"/>
                    </svg>

                    <md-icon class="ico" name="microphone-muted_12" v-if="muted2" :color="!controlOffRedStyle ? 'red-60' : 'white'" />
                    <span class="lb" v-if="!controlOffRedStyle" >{{muted2 ? 'Unmute' : 'Mute'}}</span>
                    <span class="lb" v-if="controlOffRedStyle === 1" >{{muted2 ? 'Off' : 'On'}}</span>
                </div>
                <div class="btn btn-more" ref="btnmore" @click="showMenu">
                    <md-icon name="more_12" />
                </div>
            </div>

        </div>
        
        <!--div class='ico_host' v-if='host && !isBgVideo && videoLayoutMode!="side-by-side" && videoLayoutMode!="fullscreen"' ></div-->
        <div class='as_border' v-if=' !isBgVideo && !maximizeVideo && peopleList.length > 2 && activeSpeakerName==name' ></div>
        <!--div class='as_border_thin' v-if='(!isBgVideo && videoLayoutMode=="strip" && peopleList.length > 2 && currentSharing!==null && activeSpeakerName==name && !videoSharingSwitched) || (!isBgVideo && videoLayoutMode=="strip" && activeSpeakerName==name && activeSpeakerName!=lockedPeopleName && lockedPeopleName!=null)' ></div-->

    </div>
</template>

<script>


export default {
    props:[
        'isBgVideo',
        'isMaxVideo',
        'name',
        'host',
        'avatar',
        'video',
        'audio',
        'muted',
        'sidebysideTop',
    ],
    data(){
        return {
            ctrlVisible: false,
            muted2: this.muted,
        }
    },
    mounted:function(){
        if (this.name === this.me.name) {
            this.initCam();
        }
    },
    computed:{
        theme() {
            return this.$store.state.theme;
        },
        controlOffRedStyle(){
            return this.$store.state.controlOffRedStyle
        },
        selfVideoWithOthers(){
            return this.$store.state.selfVideoWithOthers
        },
        gridViewShowVideoOnly(){
            return this.$store.state.gridViewShowVideoOnly
        },
        cameraOn(){
            return this.$store.state.cameraOn
        },
        me(){
            return this.$store.getters.me
        },
        peopleList(){
            return this.$store.state.peopleList;
        },
        maximizeVideo(){
            return this.$store.state.maximizeVideo;
        },
        menuVideoVisible(){
            return this.$store.state.menuVideoVisible;
        },
        videoVisible(){
            return !(this.videoLayoutMode == 'side-by-side' && this.panelVisible && this.activeSpeakerName != this.name)
        },
        videoLayoutMode(){
            return this.$store.state.videoLayoutMode;
        },
        activeSpeakerName(){
            return this.$store.state.activeSpeakerName;
        },
        lockedPeopleName(){
            return this.$store.state.lockedPeopleName;
        },
        backgroundColor(){
            return this.showWave || this.showWaveStop ? 'rgba(0,0,0,0)' : '#EBEBEC';
        },
        avatarUrl(){
            if(this.avatar){
                return 'url('+this.$store.state.avatarPath+this.avatar+')';
            }else{
                return 'none';
            }
        },
        abbr(){
            let arr = this.name.split(' ');
            return arr[0][0].toUpperCase() + arr[1][0].toUpperCase();
        },
        videoUrl(){
            return this.video ? this.$store.state.videoPath+this.video : '';
        },
        currentSharing(){
            return this.$store.state.currentSharing;
        },
        sideBySideVideoColumn(){
            return this.$store.state.sideBySideVideoColumn;
        },
        panelVisible(){
            return this.$store.state.panelVisible;
        },
        audioIco(){
          if(this.audio == 'mobile'){
              return 'icon-phone-iph_12';
          }else if(this.audio == 'pc'){
              return 'icon-headset_12';
          }else if(this.audio == 'phone'){
              return 'icon-handset_12';
          }else{
              return '';
          }
        },
    },
    watch: {
        muted(val) {
            this.muted2 = val;
        },
        menuVideoVisible(newVal) {
            if (!newVal) {
                this.ctrlVisible = false;
            }
        },
    },
    methods:{
        initCam() {
            const selfViewVideo = this.$refs.video;
            const constraints = { audio: false, video: { width: 1280, height: 720 } };
            navigator.mediaDevices
                .getUserMedia(constraints)
                .then(function(mediaStream) {
                const video = selfViewVideo;
                video.srcObject = mediaStream;
                video.onloadedmetadata = function(e) {
                    video.play();
                };
                })
                .catch(function(err) {
                console.log(err.name + ': ' + err.message);
                });
        },

        mute(evt) {
            evt.stopPropagation();
            evt.preventDefault();
            
            let plist = [];
            const len = this.peopleList.length;
            for (let i = 0; i < len; i++) {
                const p = this.peopleList[i];
                if (p.name === this.name) {
                    p.muted = true;
                }
                plist.push(p);
            }
            this.$store.commit("peopleList", plist);

            if (this.name === this.me.name) {
                this.$store.commit('muted', true);
            }
        },
        unmute(evt) {
            evt.stopPropagation();
            evt.preventDefault();

            let plist = [];
            const len = this.peopleList.length;
            for (let i = 0; i < len; i++) {
                const p = this.peopleList[i];
                if (p.name === this.name) {
                    p.muted = false;
                }
                plist.push(p);
            }
            this.$store.commit("peopleList", plist);

            if (this.name === this.me.name) {
                this.$store.commit('muted', false);
            }
            
        },
        mouseover(evt){
            if(this.isBgVideo){
                return;
            }
            if(this.videoLayoutMode=='fullscreen' && this.activeSpeakerName==this.name){
                return;
            }
            this.ctrlVisible = true;
        },
        mouseout(evt){
            if(this.ctrlVisible && !this.menuVideoVisible){
                 this.ctrlVisible = false;
            }
        },

        showMenu(evt){
            evt.stopPropagation();

            const appos = document.querySelector("#desktop").getBoundingClientRect();
            const offset = this.$refs.btnmore.getBoundingClientRect();
            const width = offset.width;
            const left = offset.left - appos.left + width;
            const top = this.videoLayoutMode==='strip' && !this.currentSharing && !this.maximizeVideo ? (offset.top - appos.top) : (offset.top - appos.top + offset.height);
            this.$store.commit("popStartPoint", [left, top]);
            this.$store.commit('menuVideoVisible', true);
            this.$store.commit('menuDirection', this.videoLayoutMode==='strip' && !this.currentSharing && !this.maximizeVideo ? 't' : 'b');
            this.$store.commit('menuVideoName', this.name);
        },

        toggleMaximize(evt){
            if(this.videoLayoutMode === 'strip' || this.videoLayoutMode === 'grid'){
                if (this.maximizeVideo) {
                    this.$store.commit('maximizeVideo', null);
                } else {
                    this.$store.commit('maximizeVideo', this.$store.getters.people(this.name));
                }
                
            }
        },
    }
}
</script>

