<template>
<drag-resize
  :isActive="true"
  :w="240"
  :h="135"
  :maxw="320"
  :maxh="180"
  :minw="160"
  :minh="90"
  :x="windowsSize === 'normal' ? (1184-240-16) : (1424-240-16)"
  :y="windowsSize === 'normal' ? (603-135-16) : (653-135-16)"
  :parentW="parentW"
  :parentH="parentH"
  :isDraggable="peopleList.length > 1"
  :isResizable="peopleList.length > 1"
  :aspectRatio="true"
  :parentLimitation="true"
  @resizing="onresize"
  @dragging="ondrag"
  class="dragwrap"
  :class="{full: peopleList.length === 1}"
>
  <div id='self_video' 
    ref="self_video" 
    :class="[theme]"
    @mouseenter="mouseover" 
    @mouseleave="mouseout"
  >
    <video id="camera" autoplay="true"></video>
    <div class="ctrl" v-if="ctrlVisible">
      <div class="btn btn-min" :style="{opacity: peopleList.length > 1 ? 1 : 0}"><md-icon class="ico" name="pop-in_12" /></div>
      <div class="btn btn-more" ref="btnmore" @click="showMenu"><md-icon name="more_12" /></div>
    </div>
  </div>
</drag-resize>
</template>

<script>

export default {

  data() {
    return {
      minimized: false,
      ctrlVisible: false,
      parentW: 9999,
      parentH: 9999,
    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    windowsSize() {
      return this.$store.state.windowsSize;
    },
    cameraOn() {
      return this.$store.state.cameraOn;
    },
    peopleList() {
      return this.$store.state.peopleList;
    },
    me(){
      return this.$store.getters.me
    },
    menuVideoVisible(){
      return this.$store.state.menuVideoVisible;
    },
    sidePanelIsOpen(){
      return this.$store.state.sidePanelIsOpen;
    },
    rightPanelWidth(){
      return this.$store.state.rightPanelWidth;
    },
  },
  mounted() {
    if (this.cameraOn) {
      this.initCam();
    }
  },
  watch: {
    windowsSize(val) {
      this.$nextTick(() => {
        this.calcParentLimit();
      })
    },
    sidePanelIsOpen(val) {
      this.$nextTick(() => {
        this.calcParentLimit();
      })
    },
    cameraOn(val) {
      if (val) {
        this.initCam();
      }
    },
    menuVideoVisible(newVal) {
        if (!newVal) {
            this.ctrlVisible = false;
        }
    },
  },
  methods: {
    calcParentLimit(){
      const rect = document.querySelector('#con_left_top').getBoundingClientRect();
      this.parentW = rect.width - this.rightPanelWidth;
      this.parentH = rect.height;
    },
    onresize(newRect) {
      this.$store.commit("menuVideoVisible", false);
    },
    ondrag(newRect) {
      this.$store.commit("menuVideoVisible", false);
    },
    
    initCam() {
      const selfViewVideo = document.getElementById('camera');
      const constraints = { audio: false, video: { width: 1280, height: 720 } };
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(mediaStream) {
          const video = selfViewVideo;
          video.srcObject = mediaStream;
          video.onloadedmetadata = function(e) {
            video.play();
          };
        })
        .catch(function(err) {
          console.log(err.name + ': ' + err.message);
        });
    },

    mouseover(evt){
      this.ctrlVisible = true;
    },
    mouseout(evt){
      if(this.ctrlVisible && !this.menuVideoVisible){
        this.ctrlVisible = false;
      }
    },

    showMenu(evt){
      evt.stopPropagation();

      const appos = document.querySelector("#desktop").getBoundingClientRect();
      const offset = this.$refs.btnmore.getBoundingClientRect();
      const width = offset.width;
      const left = offset.left - appos.left + width;
      let top;

      if (this.peopleList.length > 1) {
        top = offset.top - appos.top;
        this.$store.commit('menuDirection', 't');
      } else {
        top = offset.top - appos.top + offset.height;
        this.$store.commit('menuDirection', 'b');
      }

      this.$store.commit("popStartPoint", [left, top]);
      this.$store.commit('menuVideoVisible', true);
      this.$store.commit('menuVideoName', this.me.name);
    },

  },
};
</script>

<style lang="scss">
