      transform: translate(-50%, -50%) scale(-1.01, 1.01);
      width: auto;
      height: 100%;
    }

    .full {
      width: 100% !important;
      height: 100% !important;
      top: 0 !important;
      left: 0 !important;
      pointer-events: none !important;
    }
    .full #self_video {
      box-shadow: none !important;
      pointer-events: auto !important;
    }
    .full #camera {
      height: auto;
      width: 100%;
    }

    .ctrl {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 6px;
      position: absolute;
      top: 6px;
      height: 28px;
      width: 100%;
    }
    .ctrl .btn{
      height: 28px;
      width: 28px;
      border-radius: 50%;
      background-color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .ctrl .btn:hover,
    .ctrl .btn:active{
      background-color: $md-gray-10;
    }

    .btn-min .ico{
      transform: scale(-1, 1);
    }





    .wrap {
      width: 180px;
      margin-top: 6px;
      margin-bottom: 6px;
    }

    .itm{
      height: 32px;
      cursor: pointer;
      user-select: none;
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 0 15px;
      font-family: "Segoe UI";
    }
    .itm:hover{
        background: #E5FBFF;
    }
    .itm .lb{
        font-size: 14px;
        pointer-events: none;
        padding-left: 8px;
    }



    /* ------------------ drak/blend ------------------ */

    &.dark .ctrl .btn,
    &.blend .ctrl .btn {
      background-color: $md-gray-70;
    }
    &.dark .ctrl .btn.off,
    &.blend .ctrl .btn.off {
      background-color: $md-red-60;
    }


  }
