  getters: {
    activeSpeaker (state) {
      let p = state.peopleList.find(
        people => people.name === state.activeSpeakerName
      );
      let p2 = state.peopleNotJoinedList.find(
        people => people.name === state.activeSpeakerName
      );
      if (p) {
        return p;
      } else {
        return p2;
      }
    },
    host (state) {
      let p = state.peopleList.find(people => people.host);
      let p2 = state.peopleNotJoinedList.find(people => people.host === true);
      if (p) {
        return p;
      } else {
        return p2;
      }
    },
    people: state => name => {
      return state.peopleList.find(people => people.name === name) || state.peopleNotJoinedList.find(people => people.name === name);
    },
    me (state) {
      return state.peopleList.find(people => people.me);
    },
    openedPanelCnt: state => {
      let cnt = 0;
      cnt += state.plistPanelVisible && !state.plistPanelPopouted ? 1 : 0;
      cnt += state.chatPanelVisible && !state.chatPanelPopouted ? 1 : 0;
      cnt += state.pollingPanelVisible && !state.pollingPanelPopouted ? 1 : 0;
      cnt += state.ccPanelVisible && !state.ccPanelPopouted ? 1 : 0;
      return cnt;
    },
    anyOpenPanel: state => {
      return (
        state.plistPanelVisible ||
        state.chatPanelVisible ||
        state.pollingPanelVisible ||
        state.ccPanelVisible
      );
    },
    anyPopoutPanel: state => {
      return state.plistPanelPopouted ||
        state.chatPanelPopouted ||
        state.pollingPanelPopouted ||
        state.ccPanelPopouted;
    },
    allPanelsPopouted: state => {
      if (state.plistPanelVisible && !state.plistPanelPopouted) {
        return false;
      }
      if (state.chatPanelVisible && !state.chatPanelPopouted) {
        return false;
      }
      if (state.pollingPanelVisible && !state.pollingPanelPopouted) {
        return false;
      }
      if (state.ccPanelVisible && !state.ccPanelPopouted) {
        return false;
      }
      return true;
    },
    unreadMessages (state) {
      return state.chatMsgList.filter(msg => msg.unread);
    },
  },
