<template>
  <div id="browser">
    <div class='page email' v-if="visiblePage === 1">
      <div
        class="btn"
        @click="next"
      ></div>
    </div>

    <div class='page page2' v-show="visiblePage === 2">
      <div
        class="btn"
        @click="install"
      ></div>

      <div class="popinstall" v-show="installVisible">
        <div class="progressbar" >
          <div class="bar" id="web-install-progressbar" ></div>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import mframe from 'mframe'
export default {
  data() {
    return {
      visiblePage: 1,
      installVisible: false,
    };
  },
  computed: {

  },
  mounted() {

  },
  watch: {

  },
  methods: {
    next() {
      this.visiblePage = 2;
    },
    install() {
      this.installVisible = true;
      
      this.$nextTick( _ => {
        const trans2 = mframe([{
            dom: document.getElementById('web-install-progressbar'),
            frames: [
                { css: {width: '1%'}, time:0 },
                { css: {width: '100%'}, time:120, tween: 'easeInOut' }
            ]
        }], {
          end: () => {
              this.launch();
            }
        });
        trans2.play();
      });
    },
    launch() {
      this.$store.commit('browserVisible', false);
      this.$store.commit('interstitialVisible', true);
    },

  },
};
</script>

