<script>
import hitTest from "../utils/hitTest";

export default {
  name: 'Topbar',
  props:{
    type: {
      default: 'in-meeting'
    },
  },
  data () {
    return {
      networkinfo: true,
      dropmenuVisible: false,
      popmenuSwitchSharingVisible: false,
    }
  },
  mounted () {

  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    titleMenuIsOpen() {
      return this.$store.state.titleMenuIsOpen;
    },
    windowsSize() {
      return this.$store.state.windowsSize;
    },
    recordOn() {
      return this.$store.state.recordOn;
    },
    currentSharing() {
      return this.$store.state.currentSharing;
    },
    meetingTitle() {
      return this.$store.state.meetingTitle;
    },
    meetingLocked() {
      return this.$store.state.meetingLocked;
    },

  },
  watch: {

  },
  methods: {
    toggleWindowsSize(){
      if (this.windowsSize === 'max') {
        this.$store.commit("windowsSize", 'normal');
        this.$store.commit("windowsWidth", '1200px');
        this.$store.commit("windowsHeight", '720px');
      } else {
        this.$store.commit("windowsSize", 'max');
        this.$store.commit("windowsWidth", '1440px');
        this.$store.commit("windowsHeight", '770px');
      }
    },
    toggleMenu(){
      this.$store.commit("titleMenuIsOpen", !this.titleMenuIsOpen);
    },
    togglePopmenuSwitchSharing() {
      this.popmenuSwitchSharingVisible = !this.popmenuSwitchSharingVisible;
    },
    toggleInfo() {
      const appos = document.querySelector("#desktop").getBoundingClientRect();
      const offset = this.$refs.info.getBoundingClientRect();
      const left = offset.left - appos.left + offset.width/2;
      const top = offset.top - appos.top + offset.height;
      this.$store.commit("popStartPoint", [left, top]);
      this.$store.commit("popInfoVisible", !this.popInfoVisible);
    },
    showMenu() {
      this.dropmenuVisible = !this.dropmenuVisible;

      if (this.dropmenuVisible) {
        window.addEventListener("mousedown", this.onMouseDown);
      } else {
        window.removeEventListener("mousedown", this.onMouseDown);
      }

    },
    onMouseDown(evt) {
      if (this.$refs.dropmenu && !hitTest(this.$refs.dropmenu, evt.pageX, evt.pageY)) {
        this.dropmenuVisible = false;
      }
    },
    close(evt) {
      this.$emit('close', evt);
    },

  },
}
</script>

