        <div class="title">In Meeting Exploration</div>

        <div>

          <md-button
            aria-label="button"
            size="40"
            class="white"
            @click="start('attendee')"
            style="width: 300px;"
          >Attendee</md-button>

          <md-button
            aria-label="button"
            size="40"
            class="white"
            @click="start('host')"
            style="width: 300px;"
          >Host</md-button>

        </div>
        <div class="instruction">
          <div>
            * Press
            <b>Alt+A</b> (Win) /
            <b>Option+A</b> (Mac) to add participants.
          </div>

