<script>
import validateBrowser from "../utils/validateBrowser";
import { Draggable } from "draggable-vue-directive";
import getQueryString from '../utils/getQueryString'

export default {
  name: "Index",
  directives: {
    Draggable
  },
  data() {
    return {
      invalidBrowser: false,
      coverVisible: true,
      draggableValue: {},
    };
  },
  mounted() {
    if (!validateBrowser()) {
      this.invalidBrowser = true;
    }

    this.draggableValue.onDragStart = this.onDragStart;

    const controlOffRedStyle = parseInt(getQueryString('offred'));
    this.$store.commit('controlOffRedStyle', controlOffRedStyle);

    const theme = getQueryString('theme');
    if (['light', 'blend', 'dark'].indexOf(theme) > -1) {
      this.$store.commit('theme', theme);
    }

  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    peopleList() {
      return this.$store.state.peopleList;
    },
    peopleNotJoinedList() {
      return this.$store.state.peopleNotJoinedList;
    },
    interstitialVisible() {
      return this.$store.state.interstitialVisible;
    },
    browserVisible() {
      return this.$store.state.browserVisible;
    },
    meetingAppVisible() {
      return this.$store.state.meetingAppVisible;
    },
    meetingVisible() {
      return this.$store.state.meetingVisible;
    },
    plistPanelVisible() {
      return this.$store.state.plistPanelVisible;
    },
    chatPanelVisible() {
      return this.$store.state.chatPanelVisible;
    },
    ccPanelVisible() {
      return this.$store.state.ccPanelVisible;
    },
    plistPanelClsed() {
      return this.$store.state.plistPanelClsed;
    },
    chatPanelClsed() {
      return this.$store.state.chatPanelClsed;
    },
    ccPanelClsed() {
      return this.$store.state.ccPanelClsed;
    },
    plistPanelPopouted() {
      return this.$store.state.plistPanelPopouted;
    },
    chatPanelPopouted() {
      return this.$store.state.chatPanelPopouted;
    },
    ccPanelPopouted() {
      return this.$store.state.ccPanelPopouted;
    },


    pollingPanelVisible() {
      return this.$store.state.pollingPanelVisible;
    },
    pollingPanelClsed() {
      return this.$store.state.pollingPanelClsed;
    },
    pollingPanelPopouted() {
      return this.$store.state.pollingPanelPopouted;
    },

    allPanelsPopouted() {
      return this.$store.getters.allPanelsPopouted;
    },
    popoutPanelLeft() {
      return this.allPanelsPopouted ? '1066px' : '760px';
    },
  },
  watch: {},
  methods: {
    start(type) {
      if (type === 'host') { //host
        const lst = this.peopleNotJoinedList;
        for (let i=0;i <lst.length; i++) {
          if (lst[i].host) {
            this.peopleNotJoinedList[i].host = false;
            break;
          }
        }
        this.peopleList[0].host = true;
        this.$store.commit('peopleNotJoinedList', this.peopleNotJoinedList);
        this.$store.commit('peopleList', this.peopleList);
        this.$store.commit('meetingAppVisible', true);
      } else { // attendee
        const p = this.peopleNotJoinedList.splice(0, 6);
        const lst = this.peopleList.concat(p);
        this.$store.commit('selfVideoWithOthers', true);
        this.$store.commit('peopleNotJoinedList', this.peopleNotJoinedList);
        this.$store.commit('peopleList', lst);
        this.$store.commit('browserVisible', true);
      }
      
      this.coverVisible = false;
    },
    onDragStart(positionDiff, absolutePosition, event) {
      let zIndex = event.currentTarget.style['z-index'] || 99;
      event.currentTarget.style['z-index'] = zIndex + 2;
    },

  }
};
</script>

