<template>
    <div id="pop_info" v-if="popInfoVisible"
      :class="[theme]"
      :style="{
        left: (popStartPoint[0] - 1) + 'px',
        top: (popStartPoint[1] + 16) + 'px',
      }"
    >
      <div class="triangle"></div>
      <div class="wrap">
        <div class='close' @click='close' >
          <span class="md-icon icon icon-cancel_12"></span>
        </div>
        <div class="title">{{ meetingTitle }}</div>
        <div class="ava">
          <div class="avatar" v-bind:style="{backgroundImage: avatarUrl}"></div>
          <span class="name">{{ hostName }}</span>
        </div>
        <div class="sec">
          <button
            type="button"
            class="md-button md-button--blue"
          >Copy Meeting Link</button>
          <button
            type="button"
            class="md-button"
            :class="{'md-button--dark': theme === 'dark'}"
          >Invite & Remind</button>
        </div>
        <div class="sec">
          <h1>Meeting number</h1>
          <p>123 456 789</p>
        </div>
        <div class="sec">
          <h1>Video address</h1>
          <p>424535@go.webex.com</p>
        </div>
        <div class="sec">
          <h1>Audio connection</h1>
          <p>US TOll +1-415-655-0000</p>
          <p class="md-link md-link--blue">Show all global call-in numbers</p>
        </div>
        <div class="sec">
          <h1>Access code</h1>
          <p>343 924 136</p>
        </div>
        <div class="sec">
          <h1>Access ID</h1>
          <p>13</p>
        </div>
      </div>
    </div>
</template>

<script>
import hitTest from '../utils/hitTest';

export default {
  data() {
    return {};
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    host() {
      let people = this.$store.getters.host;
      return people;
    },
    hostName() {
      return this.host.name;
    },
    meetingTitle() {
      return this.$store.state.meetingTitle;
    },
    popInfoVisible() {
      return this.$store.state.popInfoVisible;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },
    avatarUrl() {
      if (this.host.avatar) {
        return "url(" + this.$store.state.avatarPath + this.host.avatar + ")";
      } else {
        return "none";
      }
    }
  },
  mounted() {},
  watch: {
    popInfoVisible(newVal) {
      if (newVal) {
        window.addEventListener("mousedown", this.onMouseDown);
      } else {
        window.removeEventListener("mousedown", this.onMouseDown);
      }
    }
  },
  methods: {
    onMouseDown(evt) {
      if (
        !hitTest(document.querySelector("#mainclient .btn_info"), evt.pageX, evt.pageY) &&
        !hitTest(document.querySelector("#pop_info"), evt.pageX, evt.pageY)
      ) {
        this.$store.commit("popInfoVisible", false);
      }
    },
    close() {
      this.$store.commit("popInfoVisible", false);
    }
  }
};
</script>

