<template>
  <div id="pop_panel" v-if="popPanelVisible"
    :class="[theme]"
    :style="{
      left: (popStartPoint[0] + 37) + 'px',
      top: (popStartPoint[1] - 20) + 'px',
    }"
  >
    <div class="triangle"></div>
    <div class="wrap">
      <div class="menuitm" @click="togglePolling">
        <md-icon name="poll_16" :color="pollingPanelVisible ? 'blue' : ''" />
        <span class="lb" :class="{on: pollingPanelVisible}" >Polling</span>
      </div>
      <div class="menuitm">
        <span class="md-icon icon icon-q-a_16"></span>
        <span class="lb">Q&A</span>
      </div>
      <div class="menuitm">
        <span class="md-icon icon icon-note_16"></span>
        <span class="lb">Notes</span>
      </div>
      <div class="menuitm" @click="toggleCC" v-if="transcriptEnabled">
        <md-icon name="transcript_16" :color="ccPanelVisible ? 'blue' : ''" />
        <span class="lb" :class="{on: ccPanelVisible}" >Captions and Highlights</span>
      </div>
      <div class="menuitm">
        <span class="md-icon icon icon-multimedia_16"></span>
        <span class="lb">Media Viewer</span>
      </div>
      
    </div>
  </div>
</template>

<script>
import hitTest from '../utils/hitTest';

export default {
  data() {
    return {};
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    popPanelVisible() {
      return this.$store.state.popPanelVisible;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },
    pollingPanelVisible() {
      return this.$store.state.pollingPanelVisible;
    },
    closedCaptionEnabled() {
      return this.$store.state.closedCaptionEnabled;
    },
    transcriptEnabled() {
      return this.$store.state.transcriptEnabled;
    },
    ccPanelVisible() {
      return this.$store.state.ccPanelVisible;
    },

  },
  mounted() {},
  watch: {
    popPanelVisible(newVal) {
      if (newVal) {
        window.addEventListener("mousedown", this.onMouseDown);
      } else {
        window.removeEventListener("mousedown", this.onMouseDown);
      }
    }
  },
  methods: {
    onMouseDown(evt) {
      if (
        !hitTest(document.querySelector(".btn_panels"), evt.pageX, evt.pageY) &&
        !hitTest(document.querySelector("#pop_panel"), evt.pageX, evt.pageY)
      ) {
        this.$store.commit("popPanelVisible", false);
      }
    },
    close() {
      this.$store.commit("popPanelVisible", false);
    },
    togglePolling(evt) {
      this.$store.commit("pollingPanelVisible", !this.pollingPanelVisible);
      this.close();
    },
    toggleCC(evt) {
      this.$store.commit("ccPanelVisible", !this.ccPanelVisible);
      this.close();
    },
  }
};
</script>

