  transition: all 0.2s ease-out;
  position: absolute;
  width: 222px;
  background-color: #fff;
  border-radius: 5px;
  transform: rotate(0deg);
  transform: translate(-100%, -100%);
  pointer-events: auto;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.5), 0px 4px 12px rgba(0, 0, 0, 0.15);

  .triangle {
    position: absolute;
    width: 50px;
    height: 20px;
    left: 159px;
    bottom: -19px;
    background: url(../assets/img/pop_triangle_down.svg) no-repeat 0 0;
  }
  .wrap {
    border-radius: 5px;
    overflow: hidden;
  }
  .menuitm .on {
    color: #00A0D1;
  }


  /* ------------------ drak ------------------ */

  &.dark {
    background: $md-gray-90;
    color: $text-color-primary-dark-ui;
  }
  &.dark .triangle {
    background: url(../assets/img/pop_triangle_black_down.svg) no-repeat 0 0;
  }
}

</style>
