<template>
    <div class='right-panel-header' :class="[theme]">
        <div class='cls' v-if="!floating" @click.stop='collapse' >
          <span class="md-icon icon" :class="{
            'icon-arrow-right_12': collapsed,
            'icon-arrow-down_12': !collapsed,
          }"></span>
        </div>
        <div class='title'>{{ title }}</div>
        <div class='popout' @click.stop='popout' >
          <span class="md-icon icon icon-pop-out_12" v-if="!floating"></span>
          <span class="md-icon icon icon-pop-in_12" v-if="floating"></span>
        </div>
        <div class='close' @click.stop='close' >
          <span class="md-icon icon icon-cancel_12"></span>
        </div>
    </div>
</template>

<script>


export default {
  props:[
    'title',
    'type',
    'floating',
  ],
  data() {
    return {
    }
  },
  computed:{
    theme() {
      return this.$store.state.theme;
    },
    plistPanelClsed(){
        return this.$store.state.plistPanelClsed;
    },
    chatPanelClsed(){
        return this.$store.state.chatPanelClsed;
    },
    pollingPanelClsed(){
        return this.$store.state.pollingPanelClsed;
    },
    ccPanelClsed(){
        return this.$store.state.ccPanelClsed;
    },
    collapsed(){
      if(this.type=='plist' && this.plistPanelClsed){
        return true;
      }else if(this.type=='chat' && this.chatPanelClsed){
        return true;
      }else if(this.type=='polling' && this.pollingPanelClsed){
        return true;
      }else if(this.type=='cc' && this.ccPanelClsed){
        return true;
      }else{
        return false;
      }
    },
  },
  mounted(){

  },
  watch: {

  },
  methods:{
    close(){
      this.$emit('close')
    },
    popout(){
      this.$emit('popout')
    },
    collapse(){
      this.$emit('collapse')
    },
  }
}
</script>


