  position: relative;
  width: 100%;
  height: 36px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-left: 6px;
  user-select: none;


  .cls {
    width: 26px;
    height: 26px;
    pointer-events: auto;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 0;
    flex-shrink: 0;
  }

  .title {
    width: 100%;
    height: 100%;
    line-height: 36px;
    font-size: 14px;
    flex-grow: 1;
    flex-shrink: 1;
    margin-left: 2px;
    font-family: "Segoe UI Semibold";
  }
  .close,
  .popout {
    display: flex;
    width: 28px;
    height: 100%;
    pointer-events: auto;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    flex-grow: 0;
    flex-shrink: 0;
  }
  .close {
    margin-right: 5px;
  }


  /* theme ----- dark/blend */
  &.blend{
    color: $text-color-primary-light-ui;
  }

}

</style>
