<template>
    <div id="pop-ai-bubble" :class="[theme]">
      <div class="triangle"></div>
      <div class="wrap">
        
        <div class="head" >
          <md-icon name="help-circle_14" style="margin-right: 10px;" />
          <div class='ico' @click.stop='close'><md-icon name='cancel_14'/></div>
        </div>
        <div class="title">Hi, I’m the Webex Assistant!</div>
        <div class="con">I can help you make your meetings more productive with highlights and closed captions.</div>
        <md-button @click="toggleCC" aria-label="Turn on Webex Assistant " color="blue" style="width: 252px; font-size:14px; height: 32px; line-height: 20px;">Turn on Webex Assistant</md-button>
      </div>
    </div>
</template>

<script>
import hitTest from '../utils/hitTest';

export default {
  props: {
    
  },
  data() {
    return {};
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },

  },
  mounted() {
    window.addEventListener("mousedown", this.onMouseDown);
  },
  destroyed() {
    window.removeEventListener("mousedown", this.onMouseDown);
  },
  watch: {
    
  },
  methods: {
    onMouseDown(evt) {
      if (
        !hitTest(document.querySelector("#ctrl-ai-button"), evt.pageX, evt.pageY) && 
        !hitTest(document.querySelector("#pop-ai-bubble"), evt.pageX, evt.pageY)
      ) {
        this.close();
      }
    },
    close() {
      this.$emit('close');
    },
    toggleCC(evt) {
      this.$store.commit("transcriptEnabled", true);
      this.$store.commit("closedCaptionEnabled", true);
      this.$store.commit("ccPanelVisible", true);
      this.close();



      setTimeout(() => {
        this.$store.commit('closedCaptionAdded', true);
      }, 5000);
    },

  }
};
</script>

